<template>
    <div id="mainType" v-if='showMain'>
      <el-tree
            v-loading="loading"
            highlight-current
            :data="treeData"
            :props="defaultProps"
            @node-click="handleNodeClick"
            @node-expand="expandNode">
      </el-tree>
      <!-- <template v-for="config in formateArr">
          <el-row style="margin-bottom:10px">
            <el-col :span='6'>
              <el-button style="width:300px" @click='showTermsOfType(config[0])' v-if='config[0]==null?false:true'>
                 {{config[0]}}
              </el-button>
            </el-col>
            <el-col :span='6'>
              <el-button style="width:300px" @click='showTermsOfType(config[1])' v-if='config[1]==null?false:true'>
                 {{config[1]}}
              </el-button>
            </el-col>
            <el-col :span='6'>
              <el-button style="width:300px" @click='showTermsOfType(config[2])' v-if='config[2]==null?false:true'>
                 {{config[2]}}
              </el-button>
            </el-col>
            <el-col :span='6'>
              <el-button style="width:300px" @click='showTermsOfType(config[3])' v-if='config[3]==null?false:true'>
                 {{config[3]}}
               </el-button>
             </el-col>
          </el-row>
      </template> -->
    </div>
    <div id="detailTermOfType" v-else-if='showDetail'>
      <modal-type-detail ref='typeDialog' v-bind:config='selectItem' v-bind:submit='submitType'></modal-type-detail>
      <div style="display:inline">
        <span>类别</span>
        <span style="color:#FF0000">{{currentType}}</span>
        <span>下共</span>
        <span style="color:#FF0000">{{totalItems}}</span>
        <span>条记录</span>
        <el-button type="primary" size="small" @click='goBack'>返回上级</el-button>
      </div>
      <template v-for="item in termArr">
        <el-row style="margin-bottom:10px">
          <el-col :span="8">
            <el-button style="width:300px" 
              @click='showTypeDialog(item[0])' v-if='item[0]==null?false:true'>
              <span style="float: left">{{item[0].term}}</span>
              <span style="float:right;color:#409EFF; font-size: 13px">{{item[0].type}}</span>
            </el-button>
            <i class="el-icon-view" v-if='item[0]==null?false:true' @click="showTermTable(item[0])"></i>
          </el-col>
          <el-col :span="8">
            <el-button style="width:300px" 
              @click='showTypeDialog(item[1])' v-if='item[1]==null?false:true'>
              <span style="float: left">{{item[1].term}}</span>
              <span style="float:right;color:#409EFF; font-size: 13px">{{item[1].type}}</span>
            </el-button>
            <i class="el-icon-view" v-if='item[1]==null?false:true' @click="showTermTable(item[1])"></i>
          </el-col>
          <el-col :span="8">
            <el-button style="width:300px" 
              @click='showTypeDialog(item[2])' v-if='item[2]==null?false:true'>
              <span style="float: left">{{item[2].term}}</span>
              <span style="float:right;color:#409EFF; font-size: 13px">{{item[2].type}}</span>
            </el-button>
            <i class="el-icon-view" v-if='item[2]==null?false:true' @click="showTermTable(item[2])"></i>
          </el-col>
        </el-row>
      </template>
      <el-button style="width:90%" @click='getMoreTerm' v-if='showBtn'>获取更多。。。</el-button>
        <modal-annotation ref='annotationDialog' v-bind:AnnotationDatas='newTermDatas' v-bind:bratConfig='bratConfig'></modal-annotation>
    </div>
</template>
<script>
    import ModalAnnotation from '@/components/ModalAnnotation.vue'
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTypeDetail from '@/components/ModalTypeDetail.vue'
    import {config,configFromDb,getTerms,updateType} from '@/api/annotation'
    import {getTypesById,getTermAnnotationsByType} from '@/api/type'
    export default{
      data () {
        return {
            loading:false,
            newTermDatas:null,
            dialogTableVisible:false,
            pageIndex:1,
            pageSize:28,
            showDetail:false,
            showMain:true,
            showBtn:true,
            configData:null,
            formateArr:[],
            configArr:[],
            fourArr:[],
            threeArr:[],
            currentType:null,
            termArr:[],
            totalItems:null,
            selectItem:{
              termId:null,
              term:null,
              type:null,
              types:[]
            },
            treeData:[],
            defaultProps: 
            {
              children: 'children',
              label: 'typeName'
            },
            bratConfig:null
        }
      },
      created () {
        var self=this
        self.initTree()
      },
      methods: {
        showTermTable(currentItem){
          var self=this;
          self.newTermDatas=null
          config()
          .then((response)=>{
            self.bratConfig=response;
            getTermAnnotationsByType(currentItem.type,currentItem.term)
            .then((response)=>{
              self.newTermDatas=response.data
            })
            self.$refs.annotationDialog.open();
          })   
        },
        loadNode(node){
          var self=this
          self.loading=true
          getTypesById(node.Id)
          .then((response)=>{
            node.children=[]
            for(var j=0;j<response.data.length;j++){
              var childData = response.data[j]
              childData.children = []
              for (var i = 0; i<response.data[j].hasChildren; i++){
                childData.children.push({})
              }
              childData.typeName=childData.typeCode+"-"+childData.typeName+' -共'+childData.hasChildren+'条'
              node.children.push(childData)
            }
            self.loading=false
          })
        },
        handleNodeClick(data){
          console.log(data)
            var self=this
            self.pageSize=28;
            self.currentType=data.typeCode;
            self.getTermsByServer();   
        },
        expandNode(data){
          var self=this
          self.loadNode(data)
        },
        initTree(){
          var self=this
          self.treeData=[]
          self.loading=true
          getTypesById("0")
          .then((response)=>{
            console.log(response.data)
            for(var j=0;j<response.data.length;j++){
              var rootElement = {Id: response.data[j].id, 'typeName': response.data[j].typeCode+"-"+response.data[j].typeName+'  -共'+response.data[j].hasChildren+'条','typeCode':response.data[j].typeCode, 'children':[]}
              console.log(response.data[j].hasChildren)
              for ( var i = 0; i<response.data[j].hasChildren; i++){
                rootElement.children.push({})
              }
              self.treeData.push(rootElement)               
            }  
            self.loading=false
          })
        },
        submitType(){
          //在id="detailTermOfType"的div中刷新
          var self=this;
          if(self.$refs.typeDialog.action=="update"){
            updateType(self.selectItem.termId,self.selectItem.type)
            .then(function(response){
              if(response.succ=="true"||response.succ==true){
                self.showTermsOfType(self.currentType);
              }
              else
              {
                self.$message.error('更新失败，请重试');
              }
            })
          }
        },
        //弹出弹出框
        showTypeDialog(currentItem){
          var self=this;
          self.selectItem.term=currentItem.term
          self.selectItem.type=currentItem.type
          self.selectItem.termId=currentItem.id
          self.getTypesFromConfig()
          self.$refs.typeDialog.show(self.selectItem);
        },
        //获取更多新词
        getMoreTerm(){
          var self=this;
          if(self.pageSize>=self.totalItems){
            self.showBtn=false;
          }
          self.pageSize=self.pageSize+10;
          self.getTermsByServer();
        },
        //返回上一级
        goBack(){
          var self=this;
          self.showDetail=false;
          self.showMain=true;
          self.getConfig();
        },
        //展示某个type类别下的所有新词Dashboard的形式
        showTermsOfType(currentType){
          var self=this;
          self.pageSize=28;
          self.currentType=currentType;
          self.getTermsByServer();   
        },
        //从服务器端获取某个type类别下的所有新词
        getTermsByServer(){
          var self=this;
          self.termArr=[];        
          getTerms(self.pageSize,self.pageIndex,self.currentType)
          .then(function(response){ 
            self.totalItems=response.data.total;
            if(response.data.dataList!=null&&response.data.dataList.length>0){
              var j=0,len=response.data.dataList.length;
              for(;j<len;j++){
                self.threeArr.push(response.data.dataList[j]);
                if(self.threeArr.length%3==0){
                  self.termArr.push(self.threeArr);
                  self.threeArr=[];
                }
              }
              if(len%3!=0){
                self.termArr.push(self.threeArr);
                self.threeArr=[];
              }
              self.showDetail=true;
              self.showMain=false;
              self.showBtn=true;   
            }
          });
        },
        getConfig(){
          var self=this;
          if(self.configData==null){        
            configFromDb()
            .then((response)=>{
              console.log(response)
              self.configData=response.data
              self.getTypesForDashboard()
            })
          }
        },
        getTypesForDashboard(){
          var self=this;
          self.configArr=[];
          // var i=0,len=self.configData.entity_types.length;
          // for(;i<len;i++){
          //   if(!self.configData.entity_types[i].type.endsWith('unconfirmed')){
          //     self.configArr.push(self.configData.entity_types[i].type);
          //   }
          //   if(self.configData.entity_types[i].children){
          //     var j=0,lenij=self.configData.entity_types[i].children.length;
          //     for(;j<lenij;j++){
          //       if(!self.configData.entity_types[i].children[j].type.endsWith('unconfirmed')){
          //         var currentType=self.configData.entity_types[i].children[j].type
          //         self.configArr.push(currentType);
          //       }
          //     }
          //   }
          // }
          var i=0,len=self.configData.length        
          for(;i<len;i++){
            self.configArr.push(self.configData[i].typeCode)
          }       
          Object.keys(self.configArr).forEach(function(key){
            self.fourArr.push(self.configArr[key])
            if(self.fourArr.length%4==0){
              self.formateArr.push(self.fourArr)
              self.fourArr=[]
            }
          })
          if(self.configArr.length%4!=0){
            self.formateArr.push(self.fourArr)
            self.fourArr=[]
          }
        },
        getTypesFromConfig(){
          var self=this;
          if(self.selectItem.types==null||self.selectItem.types.length==0){
            var i=0,leni=self.configData.length;
            for(;i<leni;i++){
              self.selectItem.types.push({
                    label: self.configData[i].typeName,
                    value: self.configData[i].typeCode,
                });
            }
            // var i=0,leni=self.configData.entity_types.length;
            // for(;i<leni;i++){
            //   if(!self.configData.entity_types[i].type.endsWith('unconfirmed')){
            //       self.selectItem.types.push({
            //         label: self.configData.entity_types[i].name,
            //         value: self.configData.entity_types[i].type,
            //     });
            //   }
            //   if(self.configData.entity_types[i].children){
            //     var j=0,lenj=self.configData.entity_types[i].children.length;
            //     for(;j<lenj;j++){
            //       if(!self.configData.entity_types[i].children[j].type.endsWith('unconfirmed')){
            //         self.selectItem.types.push({
            //             label: '|--' + self.configData.entity_types[i].children[j].name,
            //             value: self.configData.entity_types[i].children[j].type,
            //         });
            //       }
            //     }
            //   }
            // }
          }
        }   
      },
      components:{
        'modal-type-detail':ModalTypeDetail,
        'malgo-brat':MalgoBrat,
        'modal-annotation':ModalAnnotation
      }
    }
</script>


