<template>
  <div>
  <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
  </modal-term-detail>
  <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' 
    v-bind:submit='submitDeleteTerm'/>
<modal-update-type ref='typeUpdateDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitUpdateType'></modal-update-type>
  <el-dialog ref='term-table' title="标注术语列表" :visible.sync='dialogTableVisible' size="large" @close="dialogClose">
    <div id="mark-result">
      <el-table
        element-loading-text="加载数据"
        v-loading="loading"
        :data="AnnotationDatas"
        style="width:100%"  
        empty-text="暂无数据"
        height="550"
        highlight-current-row>
        <el-table-column
      type="index"
      width="50">
    </el-table-column>
        <el-table-column  min-width="100px" label="术语ID" header-align="center">
          <template slot-scope="brat">
              <span>{{brat.row.id}}</span>
          </template>
        </el-table-column>
        <el-table-column min-width="300px" label="最终标注" header-align="center">
          <template slot-scope="brat">
              <malgo-brat 
                v-bind:brat-data='brat.row.bratData' 
                :brat-config-data='bratConfig' 
                :on-select-text='selectText'
                :on-select-label='selectLabel'
                :on-update-label='updateLabel'
                :brat-id='brat.row.id'>
              </malgo-brat>
            </template>
        </el-table-column>
        <el-table-column min-width="120px" label="更新时间" header-align="center">
          <template slot-scope="brat">
              <span>{{brat.row.gmtModified}}</span>
          </template>
        </el-table-column>
      </el-table>
      <el-button-group>
        <el-button type="primary" icon="arrow-left" size="mini" @click="prevPage" :disabled="prev">上一页</el-button>
        <el-button type="primary" size="mini">{{currenIndex}}</el-button>
        <el-button type="primary" size="mini" @click="nextPage" :disabled="next">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
      <!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination> -->
    </div>   
  </el-dialog>
</div>
</template>
<script>
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'
    import ModalUpdateType from '@/components/ModalUpdateType.vue'
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import {configFromDb,addFinalAnno,deleteFinalAnnotation,updateFinalAnnotation} from '@/api/annotation'
    import {getTermAnnotationsByType,queryAnnotationServerPagination} from '@/api/type'
    export default {
      data () {
        return {
          bratConfig:null,
          loading:true,
          dialogTableVisible: false,
          AnnotationDatas: [],
          selectedTerm: {
            term: null,
            types: [],
            isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null
          },
          param:null,
          pageIndex:1,
          totalLimit:0,
          pageSize:10,
          annotationArr:[],
          currenIndex:1,
          prev:false,
          next:false,
          depth:0,
          prefix:'| ---------------------------------------------------------------'
        };
      },
      mounted () {
      },
      updated() {
      },
      methods: {
        prevPage(){
          var self=this
          self.next=false
          self.currenIndex--
          if(self.currenIndex==0){
            self.prev=true
            self.currenIndex=1
            return
          }else{
            self.AnnotationDatas=null
            self.loading=true
            clearTimeout(self.timeout);
            self.timeout=setTimeout(() => {
              self.loading=false
              self.AnnotationDatas=self.annotationArr[self.currenIndex-1]
            }, 2000);
          }
        },
        nextPage(){
          var self=this
          self.prev=false
          self.currenIndex++
          if(self.AnnotationDatas.length<10){
            self.next=true
            self.currenIndex--
            return
          }
          if(self.currenIndex<=self.annotationArr.length)
          {
            self.AnnotationDatas=null
            self.loading=true
            clearTimeout(self.timeout);
            self.timeout=setTimeout(() => {
              self.loading=false
              self.AnnotationDatas=self.annotationArr[self.currenIndex-1]
            }, 2000);
          }else{
            self.getAnnotationFromServer()  
          }
        },
        dialogClose(){
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.getAnnotationFromServer();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.getAnnotationFromServer();
        },
        getAnnotationFromServer(){
          var self=this
          self.AnnotationDatas=[]
          self.loading=true
          console.log('调用后台的分页服务')
          queryAnnotationServerPagination(self.param.type,self.param.term,self.totalLimit,self.pageSize)
          .then((response)=>{
            if(response.succ==true){
              self.AnnotationDatas = response.data.dataList
              self.loading = false
              self.totalLimit=response.data.total
              self.annotationArr.push(self.AnnotationDatas)
              console.log(self.annotationArr)
            }
          })
        },
        submitTerm(){
          var self=this
          addFinalAnno(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId,self.selectedTerm.isNewTerm?'NEW_TERM':'ANNOTATION')
          .then((response)=>{      
            if(response.succ==true){
                self.annotationArr=[]
                self.currenIndex=1
                self.totalLimit=0       
                self.getAnnotationFromServer()       
            }
          });
        },
        //单击注解
        submitUpdateType(){
          var self=this
          console.log(self.selectedTerm)
          updateFinalAnnotation(self.currentBratId,self.selectedTerm.tag,self.selectedTerm.type,
            self.selectedTerm.newType)
          .then((response)=>{
            if(response.succ==true){
              self.annotationArr=[]
              self.currenIndex=1
              self.totalLimit=0     
              self.getAnnotationFromServer();
            }
          })
        },
        //铲除新词的pros函数
        submitDeleteTerm(){
          var self=this;
          deleteFinalAnnotation(self.currentBratId,self.selectedTerm.tag,self.$refs.termDeleteDialog.addChecked?'NEW_TERM':'ANNOTATION')
          .then(function(response){
            if(response.succ==true){
              self.annotationArr=[]
              self.currenIndex=1
              self.totalLimit=0     
              self.getAnnotationFromServer();
            }
          });
        },
        selectText(data, callback){
          var self=this        
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.getTypesFromConfig();
          self.$refs.termDialog.open(self.selectedTerm);
          self.submitTermCallBack=callback;         
        },
        selectLabel(data){
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        },
        updateLabel(data){
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.currentBratState=data.currentBratState;
          self.selectedTerm.tag=data.tag;
          self.getTypesFromConfig();
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.typeUpdateDialog.open(self.selectedTerm);
          }
        },
        recursiveType(id,dataArr){
          var self=this
          self.depth++
          var prefix=self.prefix.substring(0,(self.depth-1)*4)
          for(var k=0;k<dataArr.length;k++){
            if(dataArr[k].parentId==id){
              if(!dataArr[k].typeCode.endsWith('-unconfirmed')){
                self.selectedTerm.types.push({
                  label: prefix+dataArr[k].typeName,
                  value: dataArr[k].typeCode
                })
              }
              if(dataArr[k].hasChildren>0){
                self.recursiveType(dataArr[k].id,dataArr)
              }
            }
          }
          self.depth--
        },
        getTypesFromConfig(){
          var self=this
          if(self.selectedTerm.types==null||self.selectedTerm.types.length==0){
            configFromDb()
            .then((response)=>{
                var serverTypes=response.data
                self.recursiveType('0',serverTypes)
            })
          }
        },
        open(currentParam,getBratConfig) {
          var self=this
          self.param=currentParam
          self.dialogTableVisible = true
          self.currenIndex=1
          self.totalLimit=0
          self.annotationArr=[]
          self.loading = true
          // getBratConfig().then((response) => {
          //     self.bratConfig = response.data  
          //     self.getAnnotationFromServer()
          //   }
          // )
          if (self.bratConfig == null){
            getBratConfig().then((response) => {
                self.bratConfig = response.data
                self.getAnnotationFromServer()
              }
            )
          }else{
              self.getAnnotationFromServer()
          }
        },
        mySubmit: function(){
          var self=this
          self.dialogTableVisible = false
          self.submit()
        }
      },
      components:{
        'modal-term-detail':ModalTermDetail,
        'malgo-brat':MalgoBrat,
        'modal-delete-term':ModalDeleteTerm,
        'modal-update-type':ModalUpdateType
      }
    }
</script>


