<template>
  <el-dialog ref='term-delete' title="标注数据" :visible.sync='visible' size="tiny">
    <el-form   :model="annotation">
      <el-form-item style="text-align:center">
        <span>确认要删除{{'"'+annotation.term+'"'}}上的标注{{'"'+annotation.type+'"'}}吗?</span>
      </el-form-item>
      <el-form-item >
        <!-- <el-checkbox v-model="addChecked">同时在新添加词库中删除</el-checkbox> -->
      </el-form-item>
      <el-form-item style="text-align:center">
        <el-button type="danger" @click="mySubmit">删除标注</el-button>
        <el-button @click='visible=false'>关闭</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false,
          addChecked:false,
          excludeChecked:false
        };
      },
      methods: {
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        }
      }
    }
</script>


