<template>
      <div id="main">
        <el-row  :gutter="1" type="flex" align="middle" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
          <el-col :span="2">
            <el-button type="danger" @click='handleAddDraw' icon="plus">新增</el-button> 
          </el-col>
          <el-col :span="4" >
            <el-select v-model="condition.type" placeholder="术语类型" style="width:100%"
              filterable @change="typeChange" clearable >
              <el-option
                v-for="item in condition.types"
                :key="item.typeCode"
                :label="item.typeCode"
                :value="item.typeCode">
                <span style="float: left">{{item.typeCode}}</span>
                <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeName}}</span>
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8" :offset="1">
                <el-col :span="4">
                   <el-color-picker v-model="condition.color" @change="colorChange" style="margin-top:12px;margin-bottom:0px"></el-color-picker>
                </el-col>
                <el-col class="line" :span="1">-</el-col>
                <el-col :span="18">
                  <el-input v-model="condition.color" @keyup.enter.native="colorChange" placeholder="请输入标准格式的RGB"></el-input>
                </el-col>
          </el-col>
        </el-row>
        <el-table
          v-loading="loading"
          element-loading-text="加载数据"
          :data="drawDataArr"
          style="width:100%"  
          empty-text="暂无数据"
          highlight-current-row
          >
          <el-table-column label="类型"  min-width="190"  header-align="center">
            <template slot-scope="draw">
                {{draw.row.typeCode}}
            </template>
          </el-table-column>
          <el-table-column label="渲染属性"  min-width="500" header-align="center" >
            <template slot-scope="draw">
              <template v-for="tag in draw.row.drawNameArr">
                {{tag.split(':')[0]}}
                <el-tag class="el-icon-star-off" :color="tag.split(':')[1]" v-if="tag.split(':')[0]!='dashArray'">
                </el-tag>
                <span v-else>:{{tag.split(':')[1]}}</span>
              </template>
              <!-- <el-tag :key="tag"
                    v-for="tag in draw.row.drawNameArr"
                    type="gray" :color="tag.split(':')[1]">
                    {{tag}}
              </el-tag> -->
            </template>
          </el-table-column>
          <el-table-column label="类型别名" min-width="300" header-align="center">
            <template slot-scope="draw">
                <!-- {{draw.row.typeLabel}} -->
                <el-tag :key="tag"
                        v-for="tag in draw.row.labelArr"
                        type="success">
                        {{tag}}
                </el-tag>
                <el-button class="button-new-tag" size="small" @click="handleTypeLabel(draw.row)">+Alias</el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作"  min-width="180"
            header-align="center">
            <template  slot-scope="scope">
              <el-button type="primary" size="small" icon="circle-check"
                         @click='updateDraw(scope.row)'>更新</el-button>       
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
        </el-pagination>  
      <el-dialog title="编辑渲染配置" :visible.sync='editDrawVisible' size="tiny">
        <el-form :model="currentDraw" label-position="left" label-width="70px" >
          <el-form-item label="类型">
             <el-input v-model="currentDraw.typeCode"></el-input>
          </el-form-item>
          <el-form-item label="字体颜色">
            <el-col :span="6">
              <el-color-picker v-model="currentDraw.fgColor" @change="fgColorConfirm"></el-color-picker>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="17">
              <el-input v-model="currentDraw.fgColor" placeholder="请输入标准格式的rgb"></el-input>
            </el-col>
          </el-form-item> 
          <el-form-item label="背景颜色">
            <el-col :span="6">
               <el-color-picker v-model="currentDraw.bgColor" @change="bgColorConfirm"></el-color-picker>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="17">
              <el-input v-model="currentDraw.bgColor" placeholder="请输入标准格式的rgb"></el-input>
            </el-col>
          </el-form-item>     
          <el-form-item label="dashArray">
             <el-input v-model="currentDraw.dashArray" placeholder="例如：3-3"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="cUpdateDraw">确认</el-button>
            <el-button @click='editDrawVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>  
      <el-dialog title="新增渲染配置" :visible.sync='addDrawVisible' size="tiny">
        <el-form :model="currentDraw" label-position="left" label-width="70px" >
          <el-form-item label="类型">
             <el-select v-model="currentDraw.type" placeholder="术语类型" style="width:100%"
              filterable >
              <el-option
                v-for="item in condition.types"
                :key="item.id"
                :label="item.typeCode"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="字体颜色">
            <el-col :span="6">
              <el-color-picker v-model="currentDraw.fgColor" @change="fgColorConfirm"></el-color-picker>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="17">
              <el-input v-model="currentDraw.fgColor" placeholder="请输入标准格式的rgb"></el-input>
            </el-col>
          </el-form-item> 
          <el-form-item label="背景颜色">
            <el-col :span="6">
               <el-color-picker v-model="currentDraw.bgColor" @change="bgColorConfirm"></el-color-picker>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="17">
              <el-input v-model="currentDraw.bgColor" placeholder="请输入标准格式的rgb"></el-input>
            </el-col>
          </el-form-item>     
          <el-form-item label="dashArray">
             <el-input v-model="currentDraw.dashArray" placeholder="例如：3-3"></el-input>
          </el-form-item>
          <el-form-item label="类型别名">
             <el-input v-model="currentDraw.typeLabel" placeholder="请输入对应类型的别名"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="cAddDraw">确认</el-button>
            <el-button @click='addDrawVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>  
      <el-dialog :title="aliasTitle" :visible.sync='aliasVisible' size="tiny" @close="aliasClose">
        <el-tag
          :key="tag"
          v-for="tag in alias.dynamicTags"
          :closable="true"
          :close-transition="true"
          type="success"
          @close="handleClose(tag)"
        >
        {{tag}}
        </el-tag>
        <el-input v-model="alias.currentTag" @keyup.enter.native="addAlias" placeholder="请输入其它别名">
        </el-input>
      </el-dialog>      
      </div>
</template>
<script>
    import {queryDrawByCondition,configFromDb,updateDrawByID,addDraw,updateTypeLabelByID} from '@/api/annotation'
    export default{
      data(){
        return{
          loading:false,
          drawDataArr:[],
          pageIndex:1,
          totalLimit:0,
          pageSize:10,
          condition:{
            type:null,
            color:null,
            types:[]
          },
          editDrawVisible:false,
          addDrawVisible:false,
          currentDraw:{
            id:null,
            typeCode:null,
            fgColor:null,
            bgColor:null,
            dashArray:null,
            typeLabel:null,
            type:null
          },
          drawName:null,
          alias:{
            dynamicTags:[],
            currentTag:null,
            id:null,
            serverAliaLabel:null
          },
          aliasTitle:'类型别名管理',
          aliasVisible:false
        }
      },
      created(){
        var self=this
        self.initTable()
        self.queryType()
      },
      methods:{
        addAlias(){
            var self=this
            var isR=false
            if(self.alias.currentTag){
              Object.keys(self.alias.dynamicTags).forEach((key)=>{
                if(self.alias.dynamicTags[key]==self.alias.currentTag){
                      self.$message({
                      type:'warning',
                      message:'不可重复添加'
                  });
                  isR=true
                }
              })
              if(isR)
                return
              self.alias.dynamicTags.push(self.alias.currentTag)
            }
        },
        handleClose(tag){
          var self=this
          self.alias.dynamicTags.splice(self.alias.dynamicTags.indexOf(tag), 1);
        },
        aliasClose(){
            //根据id,和dynamicTags的值，更新对应的别名
          var self=this
          if(self.alias.dynamicTags.length>0){
            var serverLabel=''
            Object.keys(self.alias.dynamicTags).forEach((key)=>{
              serverLabel+=self.alias.dynamicTags[key]+'|'
            })
            self.alias.serverAliaLabel=serverLabel.substring(0,serverLabel.length-1)
            console.log(self.alias.serverAliaLabel)
            updateTypeLabelByID(self.alias.id,self.alias.serverAliaLabel)
            .then((response)=>{
              if(response.succ==true){
                self.initTable()
              }
            })
          }
        },
        handleTypeLabel(currentRow){
          var self=this
          self.alias.dynamicTags=[]
          self.aliasVisible=true
          self.alias.id=currentRow.id
          let labelStr=currentRow.typeLabel
          if(labelStr){
            var tagStrArr=labelStr.split('|')
            Object.keys(tagStrArr).forEach((key)=>{
              self.alias.dynamicTags.push(tagStrArr[key])
            })
          }
        },
        cAddDraw(){
          var self=this
          self.joinDrawArguments()
          if(self.currentDraw.type==null)
          {
            self.$message.error('请选择类型！')
            return
          }
          addDraw(self.currentDraw.type,self.drawName,self.currentDraw.typeLabel)
          .then((response)=>{
              if(response.succ==true){
              self.$message({
                  type:'success',
                  message:'新增成功！'
              });
              self.addDrawVisible=false
              self.initTable()
            }else{
              self.$message.error(response.msg)
            }
          })
        },
        handleAddDraw(){
          var self=this
          self.addDrawVisible=true
          if(self.condition.types==null){
            self.queryType()
          }
          self.currentDraw.fgColor=null
          self.currentDraw.bgColor=null
          self.currentDraw.dashArray=null
          self.currentDraw.type=null
          self.currentDraw.typeLabel=null
        },
        fgColorConfirm(){
        },
        bgColorConfirm(){
        },
        checkColor(){
          var self=this
          if(self.currentDraw.fgColor!=null&&!self.currentDraw.fgColor.startsWith('#')){
            self.$message({
                  type:'warning',
                  message:'fgColor请输入正确格式的RGB编码'
              });
            return false
          }
          if(self.currentDraw.bgColor!=null&&!self.currentDraw.bgColor.startsWith('#')){
            self.$message({
                  type:'warning',
                  message:'bgColor请输入正确格式的RGB编码'
              });
            return false
          }
          return true
        },
        joinDrawArguments(){
          var self=this
          self.drawName=''
          console.log(self.checkColor())
          if(!self.checkColor())
            return
          if(self.currentDraw.fgColor){
             self.drawName+=`fgColor:${self.currentDraw.fgColor}`
          }
          if(self.currentDraw.bgColor){
            if(self.drawName)
              self.drawName+=','
            self.drawName+=`bgColor:${self.currentDraw.bgColor}`
          }
          if(self.currentDraw.dashArray){
            if(self.drawName)
              self.drawName+=','
            self.drawName+=`dashArray:${self.currentDraw.dashArray}`
          }
        },
        cUpdateDraw(){
          var self=this
          self.joinDrawArguments()
          updateDrawByID(self.currentDraw.id,self.drawName)
          .then((response)=>{
            if(response.succ==true){
              self.$message({
                  type:'success',
                  message:'更新成功！'
              });
              self.editDrawVisible=false
              self.initTable()
            }else{
              self.$message.error(response.msg)
            }
          })

        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.initTable();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.initTable();
        },
        queryType(){
          var self=this;
          configFromDb()
          .then((response)=>{
            console.log(response)
            self.condition.types=response.data
          })
        },
        initTable(){
          var self=this
          self.loading=true
          queryDrawByCondition(self.pageIndex,self.pageSize,self.condition.type,self.condition.color)
          .then((response)=>{
            // self.drawDataArr=response.data.dataList
            self.totalLimit=response.data.total
            var drawArr=response.data.dataList
              Object.keys(drawArr).forEach((key)=>{
                //处理drawName
                if(drawArr[key].drawName!=null&&drawArr[key].drawName!=''){
                  drawArr[key].drawNameArr=[]
                  drawArr[key].drawNameArr=drawArr[key].drawName.split(',') 
                } 
                //处理typeLable
                if(drawArr[key].typeLabel!=null&&drawArr[key].typeLabel!=''){
                  drawArr[key].labelArr=[]
                  drawArr[key].labelArr=drawArr[key].typeLabel.split('|') 
                }   
              })
            self.drawDataArr=drawArr
            self.loading=false
          })
        },
        updateDraw(currentRow){
          var self=this
          console.log(currentRow)
          self.currentDraw.id=currentRow.id
          self.currentDraw.typeCode=currentRow.typeCode
          self.currentDraw.fgColor=null
          self.currentDraw.bgColor=null
          self.currentDraw.dashArray=null
          if(currentRow.drawNameArr){
            Object.keys(currentRow.drawNameArr).forEach((key)=>{
              if(currentRow.drawNameArr[key].startsWith('fgColor')){          
                  self.currentDraw.fgColor=currentRow.drawNameArr[key].split(':')[1]
              }
              if(currentRow.drawNameArr[key].startsWith('bgColor')){       
                  self.currentDraw.bgColor=currentRow.drawNameArr[key].split(':')[1]
              }
              if(currentRow.drawNameArr[key].startsWith('dashArray')){
                  self.currentDraw.dashArray=currentRow.drawNameArr[key].split(':')[1]
              }
            })
          }
          self.editDrawVisible=true
        },
        typeChange(){
          var self=this
          console.log(self.condition.type)
          self.initTable()
        },
        colorChange(){
          var self=this
          console.log(self.condition.color)
          self.initTable()
        }
      },
      components:{
      }
    }
</script>


