import myFetch from '@/utils/myFetch'
import Qs from 'qs'
export function getConcepts(pConceptId){
	var param=Qs.stringify({
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/knowledge/getChildrenConcept.do',
		data:param
	});
}




 













