<template>
  <el-dialog ref='term-update' title="更新当前标注类型" :visible.sync='visible' size="tiny">
    <el-row>
      <el-col :span="24" style="text-align:center">
            <span>当前{{annotation.term}}文本上的标注为{{'"'+annotation.type+'"'}}</span>
      </el-col>
    </el-row>
    <el-form   :model="annotation" label-width="80px">
      <el-form-item label="类型集合">
        <el-select v-model="annotation.newType" placeholder="请选择" size="large" filterable>
          <el-option
            v-for="item in annotation.types"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            <span style="float: left">{{item.label}}</span>
            <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-row>
      <el-col :span="24" style="text-align:center">
        <el-button type="info" @click="mySubmit">确认</el-button>
        <el-button @click='visible=false'>关闭</el-button>
      </el-col>
    </el-row>
  </el-dialog>
</template>
<script>
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false,
          addChecked:false,
          excludeChecked:false
        };
      },
      methods: {
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        }
      }
    }
</script>


