<template>
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" 
            label-width="0px" style="width:350px;margin:180px auto;">
      <h3 class="title">系统登录</h3>
      <el-form-item  prop="account">
        <el-input v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item  prop="checkPass">
        <el-input type="password"v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码" @keyup.enter.native="enterClick" ></el-input>
        <!-- <input v-on:keyup.13="enterClick" type="password"v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码" style="width:98%;height:30px;border-radius:5px;"> -->
      </el-form-item>
      <el-checkbox v-model="checked" checked class="remeber" style="color:white">记住密码</el-checkbox>
      <el-form-item style="width:100%">
        <el-button type="primary" style="width:100%;" @click="handleSubmit2" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
</template>
<script>
import {login} from '@/api/login'
export default {
  name: 'hello',
  data () {
    return {
      logining: false,
      ruleForm2: {
        account:null,
        checkPass:null
      },
      rules2: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' },
        ],
        checkPass: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ]
      },
      checked: true
    }
  },
  methods:{
    enterClick(){
          var self=this
          self.handleSubmit2()
        },
    handleSubmit2() {
      var self = this;
      self.$refs.ruleForm2.validate((valid) => {
        if(valid){
          self.logining = true;
          login(self.ruleForm2.account,self.ruleForm2.checkPass)
          .then(response=>{
            if(response.succ==true||response.succ=="true")
            {
              self.logining=false;
              console.log("成功登录后台!")
              sessionStorage.setItem('user',JSON.stringify({"session":"success"}));
              self.$router.push({ path: '/uiAnnoPage' });
            }else{
              self.$message({
                message: response.msg,
                type: 'error'
              });
            }
          });
        }else{
          console.log('error submit!!');
          return false;
        }
      });
    }
  }
}
</script>


