<template>
      <div id="main" >
          <template v-for="inner in mixedArr" element-loading-text="加载数据" v-loading="contextLoading">
            <el-card class="box-card">
               <el-row>
                <el-col :span="24">
                  <el-button type="primary" @click='batchUpdate' size="small">批量更新标准术语同义词</el-button>
                </el-col>
              </el-row>
              <el-table
                :data="inner"
                style="width:100%"  
                highlight-current-row
                ref="multipleTable"
                @selection-change="shandleSelectionChange"
                >              
                <el-table-column
                  type="selection"
                  width="55">
                </el-table-column>
                <el-table-column label="术语ID" property="termId"  min-width="130"  header-align="center">
                </el-table-column>
                <el-table-column label="术语类型" property="termType" min-width="200" header-align="center">
                </el-table-column>
                <el-table-column label="原始名称" property="originName" min-width="120" header-align="center" > 
                </el-table-column> 
                <el-table-column label="术语名称" property="termName" min-width="120" header-align="center" >   
                </el-table-column>
                <el-table-column label="同义词" property="standardName" min-width="120" header-align="center">
                </el-table-column>
              </el-table>
            </el-card>
          </template>
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[4,6,8,10]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
          </el-pagination>
        <el-dialog title="批量归组" :visible.sync='synonymsVisible' size="tiny">
          <el-row style="text-align:center">
            <el-col :span="24">
               <el-switch
                  :width=90
                  @change="synonymsSwitchChange"
                  v-model="concept.action"
                  on-color="#13ce66"
                  off-color="#ff4949"
                  on-text="批量更新"
                  off-text="批量置空"
                  on-value="update"
                  off-value="cover">
                </el-switch>
            </el-col>
          </el-row>
          <el-row style="text-align:center">
            <el-col :span="24">
              <el-select v-model="concept.conceptValue" 
                filterable allow-create
                placeholder="请选择" style="width:80%" v-show="showConcepts">
                <el-option
                  v-for="item in concept.concepts"
                  :key="item.conceptId"
                  :label="item.standardName"
                  :value="item.conceptId">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row style="text-align:center">
            <el-col :span="24">
              <el-button type="primary" @click="synonymsConfirm">确认</el-button>
              <el-button @click='synonymsVisible=false'>关闭</el-button>
            </el-col>
          </el-row>
        </el-dialog>
      </div>
</template>
<script>
	import {queryTermGroupByOriginName} from '@/api/term'
  import {getAllConcepts} from '@/api/concept'
  import {batchUpdateConceptIdOfTerm} from '@/api/conceptGroup'
    export default{
      data(){
        return{
         mixedArr:[],
         pageSize:4,
         pageIndex:1,
         totalLimit:0,
         contextLoading:false,
         synonymsVisible:false,
         concept:{
          action:null,
          concepts:[],
          conceptValue:null
         },
         showConcepts:false,
         sMultipleSelection:[]
        }
      },
      created(){
      	var self=this
      	self.initCard()
      },
      methods:{
        synonymsSwitchChange(){
          var self=this
          if(self.concept.action=='update'){
            self.showConcepts=true
          }else{
            self.showConcepts=false
          }
        },
        shandleSelectionChange(val){
          var self=this
          // self.$refs.multipleTable.clearSelection()
          self.sMultipleSelection = val
        },
        synonymsConfirm(){
          var self=this
          if(self.concept.action=='cover'){
              self.concept.conceptValue=''
          }
          if(self.sMultipleSelection.length>0){
            var idArr=[]
            Object.keys(self.sMultipleSelection).forEach((key)=>{
              idArr.push(self.sMultipleSelection[key].id)
            })
            batchUpdateConceptIdOfTerm(idArr,self.concept.conceptValue)
            .then((response)=>{
              if(response.succ==true){
                self.initCard()  
                self.synonymsVisible=false
              }
            })
          }
        },
        batchUpdate(){
          var self=this
          console.log(self.sMultipleSelection)
          if(self.sMultipleSelection.length>0)
          {
            self.synonymsVisible=true
            if(self.concept.concepts.length==0){
              getAllConcepts()
              .then((response)=>{
                if(response.succ==true){
                  self.concept.concepts=response.data
                }else{
                  self.$message.error(response.msg)
                }
              })
            }
          }else{
          self.$message({
              type:'warning',
              message:'请先勾选术语记录！'
          });
         }
        },
        handleCurrentChange(val){
          var self=this
          self.pageIndex=val;
          self.initCard();
        },
        handleSizeChange(val){
          var self=this
          self.pageSize=val;
          self.initCard();
        },
        initCard(){
          var self=this
          self.contextLoading=true
          queryTermGroupByOriginName(self.pageIndex,self.pageSize)
          .then((response)=>{
            console.log(response)
            if(response.succ==true){
              self.mixedArr=response.data.mixList
              self.totalLimit=response.data.groups
            }
            self.contextLoading=false
          })
        }
      },
      components:{
       
      }
    }
</script>


