<template>
  <div>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' 
    v-bind:submit='submitDeleteTerm'/>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="2">
                <!-- <el-button type="danger" @click='getAllData' icon="search">查询</el-button> -->
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="21">
              <div id="mark-result">
                <el-table
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  @row-click="rowClick"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >
                  <el-table-column
                    type="index"
                    width="100"
                    label="标注序号">
                  </el-table-column>
                  <!-- <el-table-column label="ANID"  min-width="190"  header-align="center">
                    <template scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column> -->
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                      <!-- <div v-if="brats.row.state === 'PROCESSING'">
                        <div style="background-color:#EEEED1;border-radius: 3px;">标注中</div>
                      </div>
                      <div v-else-if="brats.row.state === 'INIT'">
                        <div style="background-color:#EEB422;border-radius: 3px;">未标注</div>
                      </div> -->
                      <div>
                        待定
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="600" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'
                        :on-select-text='selectText'
                        :on-select-label='selectLabel'>
                      </malgo-brat>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作"  min-width="180"
                    header-align="center">
                    <template  slot-scope="scope">
                      <el-button type="primary" size="small" icon="circle-check"
                                 @click='commitAnnotation(scope.$index,scope.row.id)'>提交</el-button>
                      <!-- <el-button type="success" size="small" icon="time"
                                 @click='reportSuspious(scope.$index,scope.row.id)'>待定</el-button> -->
                    </template>
                  </el-table-column>
                </el-table>       
              </div>
              <el-pagination 
                class="pagination-container" @size-change="handleSizeChange" 
                @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
                </el-pagination>
            </el-col>
            <el-col :span="3">
                  <h4 style="margin-top:1px">新添加词条</h4>
                  <ul style="border: #ddd 1px solid;
                            border-radius: 3px;
                            float: left;
                            width: 100%;
                            min-width:160px;
                            min-height:600px;
                            padding-left: 2px;
                            padding-top: 0px;
                            margin-top:-21px">
                    <li style="background-color: #455a64;
                                color: #fff;
                                border-radius: 3px;
                                cursor: default;
                                margin-right: 2px;
                                margin-top: 2px;
                                padding: 0px 12px;"
                                v-for='addedWord in currentBratData.newTerms'>
                                      {{addedWord}}<i style="
                                  cursor: pointer;
                                  float: right;
                                  font-size: 14px;
                                  margin-top: 22px;
                                  line-height: 1;
                                  margin-left: 7px;
                                  opacity: .75;
                      "class="el-icon-delete" @click="deleteNewTerm(addedWord, currentBratData.id)"></i>
                    </li>
                  </ul>
                </el-col>
        </el-row>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'
    import {config,configFromDb,queryAnnoByState,undetermined,commitAnno,addAnno,deleteAnno,delNewTerm} from '@/api/annotation'        
    export default {
      data () {
        return {
          pageIndex:1,
          totalPages:0,
          totalLimit:0,
          pageSize:10,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          currentBratId:null,
          selectedTerm: {
            term: null,
            types: [],
            // isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null
          },
          loading:false,
          currentBratData:{
            newTerms:[],
            id:null
          },
          depth:0,
          prefix:'| ---------------------------------------------------------------'
        }
      },
      created () {
          var self=this
          self.getAllData()
      },
      methods:{
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        rowClick(row){
          var self=this;
          self.currentBratData.newTerms=[];
          if(row.newTerms!=null){
            for(var i=0;i<row.newTerms.length;i++){
              var forArr=row.newTerms[i];
              self.currentBratData.newTerms.push(forArr);
            }
          } 
          self.currentBratData.id=row.id;
        },
        deleteNewTerm(currentAddedWord,currentBratId){//仅仅删除新添加词条框内对应的新词
          var self=this;
          self.$confirm('确认删除吗?', '提示')
          .then(()=>{
            var currentArrWord=currentAddedWord.split(' - ');
            delNewTerm(currentBratId,currentArrWord[0],currentArrWord[1])
            .then(function(response){
              for(var i=0;i<self.bratDatas.length;i++){
                if(self.bratDatas[i].id=self.currentBratId){
                  self.bratDatas[i].bratData=response.data.bratData
                  if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                    var forArr=response.data.newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                        Object.keys(forArr[j]).forEach(function(key){
                          self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }  
                    self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                  }else{
                    self.bratDatas[i].newTerms=[];
                    self.currentBratData.newTerms=[];
                  }
                }
              }
              self.refreshData();
            });
          }).catch(()=>{
            
          });
        },
        //双击注解,然后弹出modal,modal上的按钮事件
        submitDeleteTerm(){
          var self=this;
          deleteAnno(self.currentBratId,self.selectedTerm.tag,self.$refs.termDeleteDialog.addChecked?'NEW_TERM':'ANNOTATION')
          .then(function(response){
            for(var i=0;i<self.bratDatas.length ;i++){
              if(self.bratDatas[i].id==self.currentBratId){
                self.bratDatas[i].bratData=response.data.bratData
                if(self.$refs.termDeleteDialog.addChecked){
                  if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                    var forArr=response.data.newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                        Object.keys(forArr[j]).forEach(function(key){
                          self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }  
                    self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                  }else{
                    self.bratDatas[i].newTerms=[];
                    self.currentBratData.newTerms=[];
                  }
                }
              }
            }
            self.refreshData();
          });
        },
        //在父组件中定义该方法通过v-bind进行key/value的绑定，供子组件回调该方法
        submitTerm () {
          var self=this;
          addAnno(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId,self.selectedTerm.isNewTerm?'NEW_TERM':'ANNOTATION')
          .then(function(response){
            for(var i=0;i<self.bratDatas.length ;i++){
              if(self.bratDatas[i].id==self.currentBratId){
                self.bratDatas[i].bratData=response.data.bratData;
                if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                  var forArr=response.data.newTerms;
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                    Object.keys(forArr[j]).forEach(function(key){
                      self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }
                  self.currentBratData.newTerms=self.bratDatas[i].newTerms;
                }
              }
            }
            self.refreshData()
          });
        },
        //在父组件中定义的方法，并注入到子组件中。供子组件调用，传入相应的参数，并带参数打开modal组件
        selectText (data, callback) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.getTypesFromConfig();
          self.$refs.termDialog.open(self.selectedTerm);
          // self.submitTermCallBack=callback;
        },
        selectLabel (data) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        },
        commitAnnotation (id,bratId) {
          var self=this;
          // 返回数据中succ为true，则再次调用list.do接口，直接整个页面刷新
          commitAnno(bratId)
          .then(function(response){
              if(response.succ=="true"||response.succ==true){
                  self.$message({message:'提交成功',type:'success'});
                  self.refreshData()
              }else
              {
                self.$message.error('提交失败');
              }
          });
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          queryAnnoByState(self.pageIndex,self.pageSize,'UN_RECOGNIZE')
          .then(function(response){
            self.bratDatas=response.data.dataList;
            self.totalLimit=response.data.total;
            for(var i=0;i<self.bratDatas.length;i++){
              if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                var forArr=self.bratDatas[i].newTerms;
                self.bratDatas[i].newTerms=[];
                for(var j=0;j<forArr.length;j++){
                  if(forArr[j]!=null){
                    var obj=Object.keys(forArr[j]).forEach(function(key){
                      self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }
                }
              }
            }
            if(self.bratDatas.length>0){
              self.loading=false;
            }
          })
        },
        //调用后台的list.do接口获取bratData标注数据，以及远程读取config.json文件的bratConfig内容
        getAllData (symbol) {
          var self=this
          self.loading=true;
          self.bratDatas=null;
          if(self.bratConfig==null){
            config()
            .then(function(response){
              self.bratConfig=response.data;
              queryAnnoByState(self.pageIndex,self.pageSize,'UN_RECOGNIZE')
              .then(function(response){
                self.totalPages=response.data.pages;
                self.totalLimit=response.data.total;
                self.bratDatas=response.data.dataList;
                for(var i=0;i<self.bratDatas.length;i++){
                  if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                    var forArr=self.bratDatas[i].newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                      if(forArr[j]!=null){
                        var obj=Object.keys(forArr[j]).forEach(function(key){
                          self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                        });
                      }
                    }
                  } 
                }
                self.loading=false;
              });
            });
          }else{
            queryAnnoByState(self.pageIndex,self.pageSize,'UN_RECOGNIZE')
            .then(function(response){
              self.totalPages=response.data.pages;
              self.totalLimit=response.data.total;
              self.bratDatas=response.data.dataList;
              for(var i=0;i<self.bratDatas.length;i++){
                if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                  var forArr=self.bratDatas[i].newTerms;
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                    if(forArr[j]!=null){
                      var obj=Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }
                  }
                } 
              }
              self.loading=false;
            });
          }
        },
        //从bratConfig集合中读取entity_types中的内容并存储在selectedTerm集合中的types数组中
        recursiveType(id,dataArr){
          var self=this
          self.depth++
          var prefix=self.prefix.substring(0,(self.depth-1)*4)
          for(var k=0;k<dataArr.length;k++){
            if(dataArr[k].parentId==id){
              self.selectedTerm.types.push({
                label: prefix+dataArr[k].typeName,
                value: dataArr[k].typeCode
              })
              if(dataArr[k].hasChildren>0){
                console.log(dataArr[k].id)
                self.recursiveType(dataArr[k].id,dataArr)
              }
            }
          }
          self.depth--
        },
        getTypesFromConfig () {
          var self=this
          if(self.selectedTerm.types==null||self.selectedTerm.types.length==0){
            configFromDb()
            .then((response)=>{
                if(response.succ==true){
                var serverTypes=response.data
                self.recursiveType('0',serverTypes)
              }
            })
          }
        }   
      },
      components:{
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail,
        'modal-delete-term':ModalDeleteTerm
      }
    }
</script>


