<template>
  <div id="main">
    <el-row :gutter="10" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="3">
            <el-button type="primary" @click='handleAdd' icon="plus">新增</el-button> 
        </el-col>
        <el-col :span="4">
          <el-input v-model="condition.typeCode" placeholder="类型" style="width:100%"
          @keyup.enter.native="queryType"></el-input>
        </el-col>
        <el-col :span="4">
           <el-input v-model="condition.typeName" placeholder="类型名称" style="width:100%"
          @keyup.enter.native="queryType"></el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="24">
          <el-table
                    element-loading-text="加载数据"
                    :data="TypeDatas"
                    style="width:100%;text-align:center"  
                    empty-text="暂无数据"
                    highlight-current-row
                    >
            <el-table-column label="ID" min-width="50" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.id}}</span>
              </template>
            </el-table-column>
            <el-table-column label="类型" min-width="140" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.typeCode}}</span>
              </template>
            </el-table-column>
            <el-table-column label="类型名称" min-width="140" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.typeName}}</span>
              </template>
            </el-table-column>
            <el-table-column label="父类型" min-width="140" header-align="center">
              <template slot-scope="types">
                <span v-if="types.row.parentType!=''">{{types.row.parentType}}</span>
                <span v-else>无父类型</span>
              </template>
            </el-table-column>
            <el-table-column label="操作"  min-width="120"
                      header-align="center">
                      <template  slot-scope="scope">
                        <el-button-group>
                        <el-button type="primary" size="small" icon="edit" style="width:65px"
                                   @click='handleUpdate(scope.row)'>更新</el-button>
                        <el-button type="info" style="width:65px" size="small" @click="handleUpdateType(scope.row)">更新类型</el-button>
                        <!-- <el-button type="success" size="small" icon="delete" style="width:65px"
                                   @click='cDeleteType(scope.row.id)'>删除</el-button> -->
                        </el-button-group>
                      </template>
            </el-table-column>
          </el-table>
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
        </el-pagination>
        </el-col>
    </el-row>
    <el-dialog title="编辑类型" :visible.sync='editTypeVisible' size="tiny">
      <el-form :model="currentType" label-position="left" label-width="70px">
        <el-form-item label="原类型">
          <el-input v-model="currentType.typeCode" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="新类型">
          <el-input v-model="currentType.typeCodeNew"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button   v-loading.fullscreen.lock="fullscreenLoading"type="primary" @click="cBatchUpdateType">确认</el-button>
          <el-button @click='editTypeVisible=false'>关闭</el-button>
       </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog ref='type-detail' :title="textMap[dialogStatus]" :visible.sync='dialogTypeVisible' size="tiny">
      <el-form  :model="currentType" label-position="left" label-width="70px">
      <el-form-item style="margin-left:20%">
          <el-button v-if="dialogStatus=='add'" type="primary" @click="cAddType">确认</el-button>
          <el-button v-else type="primary" @click="cUpdateType">确认</el-button>
          <el-button @click='dialogTypeVisible=false'>关闭</el-button>
      </el-form-item>
        <el-form-item label="类型">
          <el-input v-model="currentType.typeCode" :disabled="typeShow"></el-input>
        </el-form-item>
        <el-form-item label="类型名称">
          <el-input v-model="currentType.typeName"></el-input>
        </el-form-item>
        <el-form-item label="是否父类">
          <el-switch
            @change="changeAction"
            v-model="currentType.rank"
            on-color="#13ce66"
            off-color="#ff4949"
            on-text="父类"
            off-text="子类"
            on-value="father"
            off-value="children">
        </el-switch>
        </el-form-item>
        <el-form-item label="选择父类" v-show='tree'>
          <el-input
            placeholder="输入关键字进行过滤"
            v-model="treeInput"
            @change="treeInputChange">
          </el-input>
          <el-tree
            accordion
            class="filter-tree"
            highlight-current
            :data="treeTypeArr"
            :props="defaultProps"
            :filter-node-method="filterTypeNode"
            @node-expand="expandNode"
            @node-click="handleNodeClick"
            ref="treeType">
          </el-tree>
        </el-form-item>    
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
    import {getPaginationTypes,updateBatchType,updateType,addType,deleteType,getTypes,getTypesById} from '@/api/type'
    export default{
      data(){
        return {
            fullscreenLoading:false,
            tree:false,
            dialogStatus:'',
            updateTypeId:null,
            dialogTypeVisible:false,
            editTypeVisible:false,
            action:'father',
            TypeDatas:null,
            currentType:{
              id:null,
              typeId:null,
              typeCode:null,
              typeCodeNew:null,
              typeName:null,
              rank:'father',
              type:0,
              types:[]
            },
            textMap: {
              update: '编辑类型',
              add: '新增类型'
            },
            pageSize:10,
            pageIndex:1,
            totalLimit:0,
            defaultProps: 
            {
              children: 'children',
              label: 'typeCode'
            },
            treeTypeArr:[],
            treeInput:null,
            treeId:null,
            typeShow:true,
            condition:{
              typeCode:null,
              typeName:null
            }
          }
      },
      created(){
        var self=this
        self.queryType()
      },
      
      methods:{
        handleNodeClick(data){
          var self=this
          console.log(data)
          if(self.currentType.typeCode==data.typeCode.split(" -")[0]){
            self.$message({
                  type:'warning',
                  message:'父类型与当前类型重复！'
                });
            return
          }
          self.treeInput=data.typeCode.split(" -")[0]
          self.treeId=data.id
          console.log(self.treeId)
        },
        loadNode(node){
          var self=this
          getTypesById(node.id)
          .then((response)=>{
            node.children=[]
            for(var j=0;j<response.data.length;j++){
              var childData = response.data[j]
              childData.children = []
              for (var i = 0; i<response.data[j].hasChildren; i++){
                childData.children.push({})
              }
              childData.typeCode=childData.typeCode+' -共'+childData.hasChildren+'条'
              node.children.push(childData)
            }
          })
        },
        expandNode(data){
          var self=this
          console.log('expandNode')
          self.loadNode(data)
        },
        treeInputChange(val){
          var self=this
          self.$refs.treeType.filter(val);
        },
        filterTypeNode(value, data){
          var self=this
          if(!value){
            return true;
          }
          if(data.typeCode!=null){
            return data.typeCode.indexOf(value) !== -1;  
          }   
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.queryType();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.queryType();
        },
        initTree(){
          var self=this
          self.treeTypeArr=[]
          getTypes()
          .then((response)=>{
            console.log('下拉树')
            console.log(response)
            Object.keys(response.data).forEach((key)=>{
              if(response.data[key].parentId=='0'||response.data[key].parentId==0){
                var rootElement = {id: response.data[key].id, typeCode: response.data[key].typeCode+' -共'+response.data[key].hasChildren+'条', children:[]}
                for ( var i = 0; i<response.data[key].hasChildren; i++){
                  rootElement.children.push({})
                }
                self.treeTypeArr.push(rootElement)
              }
            }) 
          })
        },
        initSelect(){
          var self=this;
          self.currentType.types=[]
          console.log(self.TypeDatas)
          var j=0,len=self.TypeDatas.length
          for(;j<len;j++){
            if(self.TypeDatas[j].typeCode==self.currentType.typeCode)
            {
              self.currentType.types.push({
              id:self.TypeDatas[j].id,
              typeCode:self.TypeDatas[j].typeCode,
              typeName:self.TypeDatas[j].typeName,
              disabled:true
              })
            }else{
              self.currentType.types.push({
                id:self.TypeDatas[j].id,
                typeCode:self.TypeDatas[j].typeCode,
                typeName:self.TypeDatas[j].typeName
              })
            }
          }
        },
        resetState(){
          var self=this;
          self.currentType.typeName=''
          self.currentType.typeCode=''
        },
        handleAdd(){
          var self=this;
          self.dialogStatus='add'
          self.dialogTypeVisible=true
          self.typeShow=false
          self.currentType.rank='father'
          self.treeInput=null
          self.tree=false
          self.resetState()
        },
        handleUpdateType(currentRow){
          var self=this
          self.currentType.typeCode=currentRow.typeCode;
          self.currentType.id=currentRow.id;
          self.editTypeVisible=true;
        },
        handleUpdate(currentRow){
          var self=this;
          self.dialogStatus='update'
          self.dialogTypeVisible=true;
          self.typeShow=true
          self.currentType.rank='father'
          self.treeInput=null
          self.tree=false
          console.log(currentRow)
          self.currentType.typeCode=currentRow.typeCode;
          self.currentType.typeName=currentRow.typeName;
          self.currentType.id=currentRow.id;
          self.currentType.originParentId=currentRow.parentId
        },
        changeAction(){
          var self=this;
          if(self.currentType.rank=='father'){
              self.tree=false;
              // self.currentType.type=0;
              self.currentType.originParentId=null
              self.treeId=null
          }
          if(self.currentType.rank=='children'){
              self.tree=true;
              // self.initSelect();
              //todo,初始化下拉树
              self.initTree();
          }
        },
        queryType(){
          var self=this;
          getPaginationTypes(self.pageIndex,self.pageSize,self.condition.typeCode,self.condition.typeName)
          .then((response)=>{
            if(response.succ==true){
              self.TypeDatas=response.data.dataList
              self.totalLimit=response.data.total
            }
          })
        },
        cAddType(){
            var self=this;
            addType(self.treeId,self.currentType.typeName,self.currentType.typeCode)
            .then((response)=>{
                if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.dialogTypeVisible=false
                  self.queryType()
                }else{
                  self.$message.error(response.msg)
                }
            })
            
        },
        cBatchUpdateType(){
          var self=this
          if(self.currentType.typeCodeNew!=null){
          self.fullscreenLoading=true
          updateBatchType(self.currentType.id,self.currentType.typeCode,self.currentType.typeCodeNew)
          .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'更新type类型成功！'
                });
                self.fullscreenLoading=false
                self.editTypeVisible=false
                self.queryType()
              }else{
                self.fullscreenLoading=false
                self.$message.error(response.msg)
              }
            })
          }
        },
        cUpdateType(){
          var self=this;
          updateType(self.treeId,self.currentType.id,self.currentType.typeName,self.currentType.originParentId)
          .then((response)=>{
            if(response.succ==true){
              if(self.currentType.typeCodeNew==null){
                  self.$message({
                  type:'success',
                  message:'更新成功！'
                 });
                self.dialogTypeVisible=false
                self.queryType()
              }  
            }else{
              self.$message.error(response.msg)
            }
          })
        },
        cDeleteType(pTypeId){
            var self=this;
            self.$confirm('确认删除该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             deleteType(pTypeId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.queryType()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
    }
</script>


