import myFetch from '@/utils/myFetch'
import Qs from 'qs'
export function fuzzyAtomicTerms(pPageIndex,pPageSize,pTerm,pType,pChecked){
	var param=Qs.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'term':pTerm,
		'type':pType,
		'checked':pChecked
	});
	return  myFetch({
		method:'post',
		url:'/atomic/concept/fuzzyList.do',
		data:param
	});
}
export function getAtomicTerms(pPageIndex,pPageSize,pTerm,pType,pID,pChecked){
	var param=Qs.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'term':pTerm,
		'type':pType,
		'id':pID,
		'checked':pChecked
	});
	return  myFetch({
		method:'post',
		url:'/atomic/concept/list.do',
		data:param
	});
}
