<template>
    <el-row class="container">
      <el-col :span="24" class="header">
        <el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
          <img src="../assets/logo.png" style="width: 100px;
          float: left;
          margin: 10px 10px 10px 10px;" />
        </el-col>
        <el-col :span="4">
          <div class="tools" @click.prevent="collapse">
              <i class="el-icon-menu"></i>
          </div>
        </el-col>
        <el-col :span="4" class="userinfo">
          <screen-full></screen-full>
          <el-dropdown trigger="hover" style="float:right">
            <span class="el-dropdown-link userinfo-inner">
              {{sysUserName}}
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>我的消息</el-dropdown-item>
              <el-dropdown-item>设置</el-dropdown-item>
              <el-dropdown-item divided >
                <span @click="logout">重新登录</span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
        <el-col :span="24" class="main">
          <aside :class="collapsed?'menu-collapsed':'menu-expanded'">
            <el-menu :default-active="$route.path" class="el-menu-vertical-demo"
                     @open="handleopen" @close="handleclose" @select="handleselect"
                     unique-opened router v-show="!collapsed" theme="dark">
              <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
                <el-submenu :index="index+''" v-if="!item.leaf">
                  <template slot="title">{{item.name}}</template>
                  <el-menu-item v-for="child in item.children" :index="child.path"
                    :key="child.path" v-if="!child.hidden">{{child.name}}
                  </el-menu-item>
                </el-submenu>
                <el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path">
                  {{item.children[0].name}}
                </el-menu-item>
              </template>
            </el-menu>
          </aside>
          <section class="content-container">
            <div class="grid-content bg-purple-light">
              <el-col :span="24" class="breadcrumb-container">
                <!-- <strong class="title">{{$route.name}}</strong> -->
                <el-breadcrumb separator="/" class="breadcrumb-inner">
                  <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                    {{ item.name }}
                  </el-breadcrumb-item>
                </el-breadcrumb>
              </el-col>
              <el-col :span="24" class="content-wrapper">
                <transition name="fade" mode="out-in">
                  <router-view></router-view>
                </transition>
              </el-col>
            </div>
          </section>
        </el-col>
      </el-col>
    </el-row>
</template>
<script>
    import ScreenFull from '@/components/ScreenFull.vue'
    export default{
      data(){
        return{
          collapsed:false,
          sysUserName:'111@111.com'
        }
      },
      created(){

      },
      methods:{
        collapse(){
          var self=this;
        },
        handleopen() {
        },
        handleclose() {
        },
        handleselect() {
        },
        //退出登录
        logout(){
          var self = this;
          self.$confirm('确认退出吗?', '提示',{
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type:'warning'
          })
          .then(() => {
            sessionStorage.removeItem('user');
            self.$router.push('/uiLogin');
            // location.reload();
          }).catch(()=>{

          });
        }
      },
      components:{
        'screen-full':ScreenFull
      }
    }
</script>


