import myFetch from '@/utils/myFetch'
import Qs from 'qs'
export function getTermsByConceptId(pPageIndex,pPageSize,pConceptId){
	var param=Qs.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/term/queryPaginationByConceptId.do',
		data:param
	});
}
export function getAtomicTermsByConceptId(pPageIndex,pPageSize,pConceptId){
	var param=Qs.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/atomic/queryPaginationByConceptId.do',
		data:param
	});
}
export function batchUpdateConceptIdOfTerm(pTermIdArr,pConceptId){
	var param=Qs.stringify({
		'termList':pTermIdArr,
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/term/updateBatchConceptIdOfTerm.do',
		data:param
	});
}
export function batchUpdateConceptIdOfAtomicTerm(pAtomicTermIdArr,pConceptId){
	var param=Qs.stringify({
		'atomicTermList':pAtomicTermIdArr,
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/atomic/updateBatchConceptIdOfAtomicTerm.do',
		data:param
	});
}





