<template>
  <div>
    <!-- <el-button type="primary" @click='initTree'>初始化</el-button>    -->
    <el-row>
      <el-col :span="16">
         <el-tree
            v-loading="loading"
            highlight-current
            :data="treeData"
            :props="defaultProps"
            @node-click="handleNodeClick"
            @node-expand="expandNode">
        </el-tree>
      </el-col>
      <el-col :span="8">
          <el-form  :model="treeForm" label-width="80px">
            <el-form-item label="名字">
              <el-input v-model="treeForm.replicaName"></el-input>
            </el-form-item>
            <el-form-item label="集合">
              <el-input v-model="treeForm.conceptType"></el-input>
            </el-form-item>
            <el-form-item label="编码">
              <el-input v-model="treeForm.conceptCode"></el-input>
            </el-form-item>
          </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
    import {getConcepts} from '@/api/knowledge' 

    export default{
      data () {
        return {
          loading:false,
          count: 0,
          treeForm:{
            conceptName:null,
            conceptCode:null,
            conceptType:null,
            replicaName:null
          },
          treeData:[],
          defaultProps: 
            {
              children: 'children',
              label: 'conceptName'
            }
          }
      },
      created () {
          var self=this
          self.initTree()
       },
      mounted(){
        
      },
      methods: {
        loadNode(node){
          var self=this
          self.loading=true
          // console.log('获取子节点')
          getConcepts(node.conceptId)
          .then((response)=>{
            node.children=[]
            for(var j=0;j<response.data.length;j++){
              var childData = response.data[j]
              childData.children = []
              for (var i = 0; i<response.data[j].hasChildren; i++){
                childData.children.push({})
              }
              childData.conceptName=childData.conceptName+' -共'+childData.hasChildren+'条'
              node.children.push(childData)
            }
            self.loading=false
          })
        },
        handleNodeClick(data){
            var self=this
            self.treeForm = data
            self.treeForm.replicaName=self.treeForm.conceptName.split("-")[0]
        },
        expandNode(data){
          var self=this
          self.loadNode(data)
        },
        initTree(){
          var self=this
          self.treeData=[]
          self.loading=true
          getConcepts("")
          .then((response)=>{
            var rootElement = {conceptId: response.data[0].conceptId, 'conceptName': response.data[0].conceptName+'  -共'+response.data[0].hasChildren+'条', 'children':[]}
            for ( var i = 0; i<response.data[0].hasChildren; i++){
              rootElement.children.push({})
            }
            self.treeData.push(rootElement)
            self.treeForm=response.data[0]
            self.treeForm.replicaName=self.treeForm.conceptName.split("-")[0]
            self.loading=false
          })
        }
      }
    }
</script>


