import myFetch from '@/utils/myFetch'
import Qs from 'qs' 
export function getAllTags(){
	var param=Qs.stringify({
	});
	return  myFetch({
		method:'post',
		url:'/tag/getAllTags.do',
		data:param
	});
}
export function addTag(pTagName){
	var param=Qs.stringify({
		'tagName':pTagName
	});
	return  myFetch({
		method:'post',
		url:'/tag/addTag.do',
		data:param
	});
}
export function addBatchTag(pTagArr){
	var param=Qs.stringify({
		'tagList':pTagArr
	});
	return  myFetch({
		method:'post',
		url:'/tag/addTags.do',
		data:param
	});
}


