  <template>
    <div id="mainQueryRegion">
      <el-row>
        <el-col :span="22">
          <el-input v-model="term" placeholder="请输入内容" style="width:90%" @keyup.enter.native="enterClick"></el-input>
        </el-col>
        <el-col :span="2">
          <el-button style="background-color:#324057;color:white" 
            icon="search" @click='getAtomicTerm'>查询</el-button>
        </el-col>
      </el-row>
      <div id="dashboardOfTermType" v-if='showDetail'>
        <modal-type-detail ref='typeDialog' v-bind:config='selectItem' v-bind:submit='submitType'></modal-type-detail>
        <div style="display:inline">
            <span>共找到</span>
            <span style="color:#FF0000">{{totalItems}}</span>
            <span>条记录</span>
        </div>
        <template v-for="item in termArr">
          <el-row :gutter="2" style="margin-bottom:15px">
            <el-col :span="12">
            <el-button style="width:400px" @click='showTypeDialog(item[0])'
              v-if='item[0]==null?false:true'>
              <span style="float:left;color:red">{{item[0].term}}</span>
              <span style="float:right;color:#409EFF; font-size: 13px">{{item[0].type}}</span>
            </el-button>
            <i class="el-icon-view" v-if='item[0]==null?false:true' @click="showTermTable(item[0])"></i>
          </el-col>
          <el-col :span="12">
            <el-button style="width:400px" @click='showTypeDialog(item[1])' 
              v-if='item[1]==null?false:true'>
              <span style="float:left;color:red">{{item[1].term}}</span>
              <span style="float:right;color:#409EFF; font-size: 13px">{{item[1].type}}</span>
            </el-button>
            <i class="el-icon-view" v-if='item[1]==null?false:true' @click="showTermTable(item[1])"></i>
          </el-col>
          </el-row>
        </template>
      </div>
      <modal-annotation ref='annotationDialog' v-bind:AnnotationDatas='newTermDatas' v-bind:bratConfig='bratConfig'></modal-annotation>
    </div>
</template>

<script>
    import ModalAnnotation from '@/components/ModalAnnotation.vue'
    import ModalTypeDetail from '@/components/ModalTypeDetail.vue'
    import {config,configFromDb,getTermsByTerm,updateType} from '@/api/annotation'
    import {getTermAnnotationsByType} from '@/api/type'
    export default{
      data(){
        return{
          bratConfig:null,
          loading:false,
          term:null,
          showDetail:false,
          pageSize:10,
          pageIndex:1,
          totalItems:null,
          twoArr:[],
          termArr:[],
          selectItem:{
            termId:null,
            term:null,
            type:null,
            types:[]
          },
          configData:null,
          newTermDatas:null,
          dialogTableVisible:false
        }
      },
      created(){

      },
      methods:{
        enterClick(){
          var self=this
          self.getAtomicTerm()
        },
        showTermTable(currentItem){
          var self=this;
          self.newTermDatas=null
          config()
          .then((response)=>{
            self.bratConfig=response;
            getTermAnnotationsByType(currentItem.type,currentItem.term)
            .then((response)=>{
                self.newTermDatas=response.data
            })
            self.$refs.annotationDialog.open();
          })
        },
        //更新或者删除新词
        submitType(){
          //在id="dashboardOfTermType"的div中刷新
          var self=this;
          if(self.$refs.typeDialog.action=="update"){
            updateType(self.selectItem.termId,self.selectItem.type)
            .then(function(response){
              if(response.succ=="true"||response.succ==true){
                self.getAtomicTerm();
              }else
              {
                self.$message.error('更新失败，请重试');
              }
            })
          }
        },
        getAtomicTerm(){
          var self=this;
          self.termArr=[];
          console.log(self.term)
          if(self.term!=null){
            getTermsByTerm(self.pageSize,self.pageIndex,self.term)
            .then(function(response){
              console.log(response)
              self.totalItems=response.data.total;
              if(response.data.dataList!=null&&response.data.dataList.length>0){
                var j=0,len=response.data.dataList.length;
                for(;j<len;j++){
                  self.twoArr.push(response.data.dataList[j]);
                  if(self.twoArr.length%2==0){
                     self.termArr.push(self.twoArr);
                     self.twoArr=[];
                  }
                }
                if(len%2==1){
                    //当前总数为奇数条
                    self.termArr.push(self.twoArr);
                    self.twoArr=[];
                }
                self.showDetail=true;   
              }
            });
          }
        },
        showTypeDialog(currentItem){
          var self=this;
          self.selectItem.term=currentItem.term
          self.selectItem.type=currentItem.type
          self.selectItem.termId=currentItem.id
          self.getConfig()
          self.$refs.typeDialog.show(self.selectItem);
        },
        getConfig(){
          var self=this;
          if(self.configData==null){
            // config()
            // .then(function(response){
            //   self.configData=response
            //   self.getTypesFromConfig()
            // })
            configFromDb()
            .then((response)=>{
              self.configData=response.data
              self.getTypesFromConfig()
            })
          }
        },
        getTypesFromConfig(){
          var self=this;
          if(self.selectItem.types==null||self.selectItem.types.length==0){
            var i=0,leni=self.configData.length;
            for(;i<leni;i++){
              self.selectItem.types.push({
                    label: self.configData[i].typeName,
                    value: self.configData[i].typeCode,
                });
            }
            // var i=0,leni=self.configData.entity_types.length;
            // for(;i<leni;i++){
            //   if(!self.configData.entity_types[i].type.endsWith('unconfirmed')){
            //       self.selectItem.types.push({
            //         label: self.configData.entity_types[i].name,
            //         value: self.configData.entity_types[i].type,
            //     });
            //   }
            //   if(self.configData.entity_types[i].children){
            //     var j=0,lenj=self.configData.entity_types[i].children.length;
            //     for(;j<lenj;j++){
            //       if(!self.configData.entity_types[i].children[j].type.endsWith('unconfirmed')){
            //         self.selectItem.types.push({
            //             label: '|--' + self.configData.entity_types[i].children[j].name,
            //             value: self.configData.entity_types[i].children[j].type,
            //         });
            //       }
            //     }
            //   }
            // }
          }
        } 
      },
      components:{
        'modal-type-detail':ModalTypeDetail,
        'modal-annotation':ModalAnnotation
      }
    }
</script>


