import myFetch from '@/utils/myFetch'
import Qs from 'qs'
export function login(username,password){
	var account=Qs.stringify({
		'accountNo':username,
		'loginPwd':password
	},{encode:false});
	// return myFetch.post('/auth/anonymous/login.do',account);
	return myFetch(
		{method:'post',
		url:'/auth/anonymous/login.do',
		data:account});
}


