import {Message,MessageBox} from 'element-ui'
const service = axios.create({
	baseURL:window.location.protocol+'//'+document.domain,//121.196.212.109:8080---annotation.malgo.cn
	// baseURL: 'http://test.annotation.malgo.cn',
	timeout:6000*10*60*24*2
})
service.interceptors.request.use(config=>{
	//添加统一请求配置
	// console.log(config)
	config.withCredentials=true// indicates whether or not cross-site Access-Control requests
	return config
  },
  error =>{
  	console.log(error)
  	Promise.reject(error)
})
service.interceptors.response.use(response=>{
	const res=response.data;
	if(res.is_succ===false){
	   MessageBox.confirm('您已被登出，请重新登录', '提示', {
             confirmButtonText: '重新登录',
             cancelButtonText: '取消',
             type: 'info'
	      })
		  .then(()=>{//调用user/actions/里的登出方法
	  		sessionStorage.removeItem('user');
	  		location.reload();
	  		// this.$router.push({ path: '/login' });
		  })
	  return Promise.reject('error');
	}else{
		//坑点，拦截器最后必须返回响应数据，不然方法调用无法得到响应数据
		return res;
	}
  },
  error=>{
  	console.log('err'+error)
    Message.error(error.message);
  	return Promise.reject(error)
  }
)
export default service


