<template>
  <div>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="2">
                <el-button type="danger" @click='batchUpdate'>批量更新</el-button>
            </el-col>
            <!-- <el-col :span="8">
              <span>手输批量更新前</span>
              <el-input-number v-model="batchNum" :step="10" 
              @change="inputChange"></el-input-number>
              <span>条</span>
            </el-col> -->
            <el-col :span="4">
              <el-select v-model="condition.state"  placeholder="请选择类型" clearable @change="enterClick">
                  <el-option
                    v-for="item in states"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="condition.user"  placeholder="请选择用户" clearable @change="enterClick">
                  <el-option
                    v-for="item in condition.users"
                    :key="item.id"
                    :label="item.accountNo"
                    :value="item.id">
                  </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
              <div id="mark-result">
                <el-table
                  ref="multipleTable"
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  @selection-change="handleSelectionChange"
                  >
                   <el-table-column
                    type="selection"
                    width="55">
                  </el-table-column>
                  <el-table-column
                    type="index"
                    min-width="120">
                  </el-table-column>
                  <el-table-column label="ANID"  min-width="120"  header-align="center">
                    <template slot-scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                        <div v-if="brats.row.state === 'PROCESSING'">标注中</div>                
                        <div v-else-if="brats.row.state === 'INIT'">待人工标注</div>                 
                        <div v-else-if="brats.row.state === 'UN_RECOGNIZE'">待定</div>
                        <div v-else-if="brats.row.state === 'FINISH'">标注完成</div>
                        <div v-else-if="brats.row.state === 'INCONSISTENT'">不一致</div>
                        <div v-else>未加密</div>
                    </template>
                  </el-table-column>
                  <el-table-column label="用户名称" min-width="120">
                    <template slot-scope="brats">
                      {{brats.row.modifier}}
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="500" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'>
                      </malgo-brat>
                    </template>
                  </el-table-column>
                  <el-table-column label="更新时间" min-width="120" header-align="center">
                    <template slot-scope="brats">
                      {{brats.row.gmtModified}}
                    </template>
                  </el-table-column>       
                </el-table>
              </div>
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,1000,2000, 3000]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
              </el-pagination>
            </el-col>
        </el-row>
        <el-dialog title="批量指派" :visible.sync='userVisible' size="tiny">
          <el-form :model="user" label-position="left" label-width="110px">
            <el-form-item label="用户列表">
              <el-select v-model="user.account" 
                filterable allow-create
                placeholder="请选择" style=" width:100%">
                <el-option
                  v-for="item in condition.users"
                  :key="item.id"
                  :label="item.accountNo"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>       
            <el-form-item>
              <el-button type="primary" @click="Confirm">确认</el-button>
              <el-button @click='userVisible=false'>关闭</el-button>
           </el-form-item>
          </el-form>
        </el-dialog>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import {config,queryAnnoByStateAndTermFuzzy,configFromDb,
      queryForDistributionAnnotation,getAccount,updateAnnotationModifier} from '@/api/annotation'      
    export default {
      data () {
        return {
          pageIndex:1,
          totalLimit:0,
          pageSize:10,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          loading:false,
          states:[
          {
            value:'INIT',
            label:'待人工标注'
          },
          {
            value:'PROCESSING',
            label:'标注中'
          },
          {
            value:'UN_RECOGNIZE',
            label:'待定'
          },
          {
            value:'FINISH',
            label:'标注完成'
          },
          {
            value:'INCONSISTENT',
            label:'不一致'
          },
          {
            value:'UN_ENCRYPTED',
            label:'未加密'
          }
          ],
          condition:{
            state:null,
            termText:null,
            users:[],
            user:null,
            states:[]
          },
          batchNum:0,
          multipleSelection:[],
          userVisible:false,
          user:{
            account:null
          }
        }
      },
      created () {
        var self=this
        self.initSelect()
      },
      methods:{
        inputChange(value){
          var self=this
          console.log(value)
          if(value>0){
            self.$refs.multipleTable.clearSelection()
          }
        },
        Confirm(){
          var self=this
          if(self.multipleSelection.length>0){
            //todo,
            console.log('multipleSelection')
            var idArr=[]
            Object.keys(self.multipleSelection).forEach((key)=>{
              idArr.push(self.multipleSelection[key].id)
            })
            self.loading=true
            updateAnnotationModifier(idArr,self.user.account)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  message:'批量更新成功！',
                  type:'success'
                })
                self.refreshData()
              }
              self.loading=false
            })
          }
          if(self.batchNum>0){
            console.log('batchNum')
            //todo,直接传入数量，更新？？no ,前端集合获取指定的数据集，传入后台，使用同一个更新接口
            // console.log('bratDatas')
            // console.log(self.bratDatas)
            // if(self.bratDatas.length>self.batchNum){
            //     var idArr=self.bratDatas.splice(0,self.batchNum)
            //     console.log(idArr)
            // }else{
            //   self.$message.error('需要批量更新的数量过大')
            // }
          }
        },
        handleSelectionChange(val){
          var self=this
          console.log(val)
          self.multipleSelection = val
          if(self.multipleSelection.length>0){
            self.batchNum=null
          }
        },
        initSelect(){
          var self=this
          getAccount()
          .then((response)=>{
            console.log(response)
            self.condition.users=response.data
          })
        },
        batchUpdate(){
            var self=this
            if(self.multipleSelection.length==0&&self.batchNum==0){
              self.$message.error('请选取要分配的标注')
              return
            }
            self.userVisible=true
        },
        enterClick(){
          var self=this
          if(self.bratConfig==null){
            self.loading=true
            config()
            .then(function(response){
              console.log(response)
              self.bratConfig=response.data
              self.refreshData()
            }) 
          }else{
            self.refreshData()
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          self.loading=true
          queryForDistributionAnnotation(self.pageIndex,self.pageSize,self.condition.state,self.condition.user)
          .then(function(response){
            self.bratDatas=response.data.dataList;
            self.totalLimit=response.data.total;
            self.loading=false;
          })
        },
        //调用后台的list.do接口获取bratData标注数据，以及远程读取config.json文件的bratConfig内容
        getAllData () {
          var self=this
          self.loading=true;
          if(self.condition.state==null)
          {
            self.$message({
              message:'请选择标注类型',
              type:'warning'
            })
              self.loading=false
              return
          }
          if(self.bratConfig==null){       
            config()
            .then(function(response){
              self.bratConfig=response;           
              queryForDistributionAnnotation(self.pageIndex,self.pageSize,self.condition.state,self.condition.user)
              .then(function(response){
                self.bratDatas=response.data.dataList;
                self.totalLimit=response.data.total;
                self.loading=false;
              });
            });
          }else{        
            queryForDistributionAnnotation(self.pageIndex,self.pageSize,self.condition.state,self.condition.user)
            .then(function(response){
              self.totalLimit=response.data.total;
              self.bratDatas=response.data.dataList;
              self.loading=false;
            });
          }
        }   
      },
      components:{
        'malgo-brat':MalgoBrat
      }
    }
</script>


