import Router from 'vue-router'
import Login from '@/components/Login'
import TabPage from '@/components/TabPage'
import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import BrowseData from '@/components/BrowseData'
import QueryType from '@/components/QueryType'
import UnRecognizePage from '@/components/UnRecognizePage'
import AnnoQueryPage from '@/components/AnnoQueryPage'
import TypeManagePage from '@/components/TypeManagePage'
import KnowledgePage from '@/components/KnowledgePage'
import ModalTerm from '@/components/ModalTerm'
import TermPage from '@/components/TermPage'
import AtomicTermPage from '@/components/AtomicTermPage'
import ConceptPage from '@/components/ConceptPage'
import DrawPage from '@/components/DrawPage'
import ConceptGroup from '@/components/ConceptGroup'
import AnnoToUserPage from '@/components/AnnoToUserPage'
import TermGroup from '@/components/TermGroup'
export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'知识模块',
      component:Container,
      leaf:true,
      children:[
        {path:'/uiKnowledgePage',component:KnowledgePage,name:'知识库'},
      ]
    },
    {
      path:'/',
      name:'用户模块',
      component:Container,
      // leaf:true,
      children:[
        {path:'/uiAnnoToUserPage',component:AnnoToUserPage,name:'分配标注'}
      ]
    },
    {
      path:'/',
      name:'标注模块',
      component:Container,
      children:[
        {path:'/uiAnnoPage',component:AnnoPage,name:'标注'},
        {path:'/uiUnRecognizePage',component:UnRecognizePage,name:'待定'},
        {path:'/uiAnnoQueryPage',component:AnnoQueryPage,name:'标注查询'},
        {path:'/uiDrawPage',component:DrawPage,name:'标注配置'}
      ]
    },
    {
      path:'/',
      name:'术语模块',
      component:Container,
      children:[
        {path:'/uiTypeManagePage',component:TypeManagePage,name:'元数据'},
        // {path:'/browseData',component:BrowseData,name:'浏览术语'},
        // {path:'/queryType',component:QueryType,name:'查询术语'},
        {path:'/uiTermPage',component:TermPage,name:'标准术语'},
        {path:'/uiAtomicTermPage',component:AtomicTermPage,name:'原子术语'},
        {path:'/uiConceptPage',component:ConceptPage,name:'概念'},
        {path:'/uiTermGroup',component:TermGroup,name:'术语组'},
        {path:'/uiConceptGroup',name:'同义词组',component:ConceptGroup,hidden:true}
      ]
    },
    {
      path:'/uiLogin',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'/',
      name:'excel操作',
      component:Container,
      leaf:true,
      children:[
        {path:'/uiExcel',component:ModalTerm,name:'excel导出'},
      ]
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/uiLogin'}
    }
  ]
})



