<template>
  <el-dialog ref='type-detail' title="词条详情" :visible.sync='visible' size="tiny">
    <el-form  :model="config">
      <el-form-item label="词条内容">
          <span style="font-size:18px">{{config.term}}</span>
      </el-form-item>
      <el-form-item label="更新类别／删除词条">
          <el-switch
            @change="changeAction"
            v-model="action"
            on-color="#13ce66"
            off-color="#ff4949"
            on-text="更新"
            off-text="删除"
            on-value="update"
            off-value="delete">
        </el-switch>
      </el-form-item>
      <el-form-item label="词条分类">
          <el-select v-model="config.type" placeholder="请选择" size="large" 
            filterable :disabled="elSelectDisable">
            <el-option
              v-for="item in config.types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
              <span style="float: left">{{item.label}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
            </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
          <el-button type="danger" @click="mySubmit" v-if="showDel">删除词条</el-button>
          <el-button type="primary" @click="mySubmit" v-else="showUpt">更新词条</el-button>
          <el-button @click='visible=false'>关闭</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
    export default{
      props: ['config','submit'],
      data(){
        return{
          visible:false,
          elSelectDisable:false,
          action:'update',
          showDel:false,
          showUpt:true
        }
      },
      created(){

      },
      methods:{
        changeAction(){//switch框改变下，设置相应的元素可见与是否可用
          var self=this;
          if(self.action=="update"){
            self.elSelectDisable=false;
            self.showUpt=true;
            self.showDel=false;
          }
          if(self.action=="delete"){
            self.elSelectDisable=true;
            self.showUpt=false;
            self.showDel=true;
          }
        },
        show(){
          var self=this;
          self.visible=true;
        },
        mySubmit(){
          var self=this;
          self.visible = false;
          self.submit();     
        }
      }
    }
</script>


