<template>
    <div>
        <el-row  :gutter="10" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="2">
          <el-button type="primary" @click='initTable' icon="search">查询</el-button>
        </el-col>
        <el-col :span="4" :offset="1">
          <el-input v-model="condition.standardName" placeholder="同义词搜索" style="width:100%"
          @keyup.enter.native="SynonymsEnter"></el-input>
        </el-col>
    </el-row>
        <el-table
        v-loading="loading"
        element-loading-text="加载数据"
        :data="conceptDataArr"
        style="width:100%;text-align:center"  
        empty-text="暂无数据"
        highlight-current-row
        >              
        <el-table-column label="标准名" property="standardName"  min-width="200"  header-align="center">
        </el-table-column>
        <!-- <el-table-column label="ConceptID" property="conceptId" min-width="160" header-align="center" >   
        </el-table-column>
        <el-table-column label="TermID" property="termId" min-width="160" header-align="center" >
        </el-table-column> -->
        <el-table-column label="操作"  min-width="150"
          header-align="center">
          <template  slot-scope="scope">     
            <el-button type="primary" size="small" 
                       @click='handleToTerm(scope.row)'>术语重绑</el-button>         
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination>
      <el-dialog title="绑定术语" :visible.sync='bindTermVisible' size="tiny">
        <el-form :model="currentConcept" label-position="left" label-width="70px">
          <el-form-item label="标准概念">
            <el-input v-model="currentConcept.standardName"></el-input>
          </el-form-item>
          <el-form-item label="已绑术语">
            <el-input v-model="currentConcept.termId"></el-input>
          </el-form-item>
          <el-form-item label="术语集合" >
            <el-autocomplete
                v-model="currentConcept.term"
                :fetch-suggestions="querySearchAsync"
                placeholder="请输入内容"
                @select="handleSelect"
                icon="search"
                style="width:100%"
              ></el-autocomplete>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cConceptConfirm">确认</el-button>
            <el-button @click='bindTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
    </div>
</template>
<script>
    import {getAllConceptByPagination,initInput,bindConcept} from '@/api/concept'
    export default{
      data(){
        return{
          conceptDataArr:null,
          loading:false,
          pageIndex:1,
          pageSize:10,
          totalLimit:0,
          condition:{
            standardName:null
          },
          bindTermVisible:false,
          currentConcept:{
            standardName:null,
            termId:null,
            term:null,
            id:null,
            bindTermId:null
          }
        }
      },
      created(){
        var self=this
        self.initTable()
      },
      methods:{
        querySearchAsync(queryString, resolve){
          var self=this
          console.log(queryString)
          clearTimeout(self.timeout);
          self.timeout=setTimeout(() => {
            if(queryString!=null&&queryString!=''){
                console.log('进入')
                initInput(queryString)
                .then((response)=>{
                console.log(response)
                if(response.succ==true){
                  for(let k of response.data){
                    k.value=k.termName
                  }
                  resolve(response.data)
                }
              })
            }
          }, 2000);
        },
        handleSelect(item){
          console.log('item')
          console.log(item)
          var self=this
          console.log(self.currentConcept.term)
           self.currentConcept.bindTermId=item.termId
        },
        cConceptConfirm(){
          var self=this
          bindConcept(self.currentConcept.id,self.currentConcept.bindTermId)
          .then((response)=>{
            if(response.succ==true){
              self.$message({
                      type:'success',
                      message:'绑定成功！'
                  });
              self.bindTermVisible=false
              self.initTable()
            }
          })
        },
        initTable(){
          var self=this
          getAllConceptByPagination(self.pageIndex,self.pageSize,self.condition.standardName)
          .then((response)=>{
            if(response.succ==true){
              self.conceptDataArr=response.data.dataList
              self.totalLimit=response.data.total
            }
          })
        },
        SynonymsEnter(){
          var self=this
          self.initTable()
        },
        handleSizeChange(val){
          var self=this
          self.pageSize=val
          self.initTable()
        },
        handleCurrentChange(val){
          var self=this
          self.pageIndex=val
          self.initTable()
        },
        handleToTerm(currentRow){
          var self=this
          var id=escape(currentRow.conceptId)
          var name=escape(currentRow.standardName)
          var params=escape(currentRow.conceptId+'@'+currentRow.standardName)
          // self.$router.push({ path: '/conceptGroup',query:{conceptId:id,standardName:name}});
          self.$router.push({ path: '/uiConceptGroup',query:{param:params}});
          self.currentConcept.standardName=currentRow.standardName
          self.currentConcept.termId=currentRow.termId
          self.currentConcept.id=currentRow.id
          self.bindTermVisible=true
        }
      },
      components:{
      }
    }
</script>


