<template>
  <div>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitDeleteTerm'/>
    <modal-update-type ref='typeUpdateDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitUpdateType'></modal-update-type>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="2">
                <el-button type="danger" @click='getAllData' icon="search">查询</el-button>
            </el-col>
            <el-col :span="4">
              <el-select v-model="condition.state"  placeholder="请选择类型" @change="enterClick">
                  <el-option
                    v-for="item in states"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-col>
            <el-col :span="4" :offset="1">
              <el-input v-model="condition.termText" placeholder="标注文本" style="width:100%"
                    @keyup.enter.native="enterClick"></el-input>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="21">
              <div id="mark-result">
                <el-table
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  @row-click="rowClick"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >
                  <el-table-column label="ANID"  min-width="190"  header-align="center">
                    <template slot-scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                        <div v-if="brats.row.state === 'PROCESSING'">标注中</div>                
                        <div v-else-if="brats.row.state === 'INIT'">待人工标注</div>                 
                        <div v-else-if="brats.row.state === 'UN_RECOGNIZE'">待定</div>
                        <div v-else-if="brats.row.state === 'FINISH'">标注完成</div>
                        <div v-else-if="brats.row.state === 'INCONSISTENT'">不一致</div>
                        <div v-else>未加密</div>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="600" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'
                        :brat-state='brats.row.state'
                        :on-select-text='selectText'
                        :on-select-label='selectLabel'
                        :on-update-label='updateLabel'>
                      </malgo-brat>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
                </el-pagination>
            </el-col>
            <el-col :span="3">
              <el-row>
                <el-col>
                  <h4 style="margin-top:1px">新添加词条:</h4>
                  <ul style="border: #ddd 1px solid;
                            border-radius: 3px;
                            float: left;
                            width: 100%;
                            min-width:160px;
                            min-height: 300px;
                            padding-left: 2px;
                            padding-top: 2px;
                            margin-top:-21px;">
                    <li style="background-color: #455a64;
                                color: #fff;
                                border-radius: 3px;
                                cursor: default;
                                margin-right: 2px;
                                margin-top: 2px;
                                padding: 7px 12px;"
                                v-for='addedWord in currentBratData.newTerms'>
                                      {{addedWord}}
                    </li>
                  </ul>
                </el-col>
                <el-col>
                   <h4 style="margin-top:-20px">备注</h4>
                    <div>
                      <ul style="border: #ddd 1px solid;
                              border-radius: 3px;
                              float: left;
                              width: 100%;
                              min-width:160px;
                              min-height: 300px;
                              padding-left: 2px;
                              padding-top: 2px;
                              margin-top:-25px;">
                        <li v-for='memo in currentBratData.memos'>{{memo.tag+' - '+memo.term+' - '+memo.type}}</li>
                      </ul>
                    </div>
                </el-col>
              </el-row>   
          </el-col>
        </el-row>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'  
    import ModalUpdateType from '@/components/ModalUpdateType.vue'
    import {config,queryAnnoByStateAndTermFuzzy,configFromDb,
      addAnno,addFinalAnno,deleteAnno,deleteFinalAnnotation,updateFinalAnnotation} from '@/api/annotation'      
    export default {
      data () {
        return {
          pageIndex:1,
          totalPages:0,
          totalLimit:0,
          pageSize:10,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          currentBratId:null,
          selectedTerm: {
            term: null,
            types: [],
            isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            newType:null,
            tag:null,
            currentBratState:null
          },
          loading:false,
          currentBratData:{
            newTerms:[],
            id:null,
            memos:[]
          },
          states:[
          {
            value:'INIT',
            label:'待人工标注'
          },
          {
            value:'PROCESSING',
            label:'标注中'
          },
          {
            value:'UN_RECOGNIZE',
            label:'待定'
          },
          {
            value:'FINISH',
            label:'标注完成'
          },
          {
            value:'INCONSISTENT',
            label:'不一致'
          },
          {
            value:'UN_ENCRYPTED',
            label:'未加密'
          }
          ],
          condition:{
            state:null,
            termText:null
          },
          depth:0,
          prefix:'| ---------------------------------------------------------------'
        }
      },
      created () {

      },
      methods:{
        enterClick(){
          var self=this
          if(self.bratConfig==null){
            self.loading=true
            config()
            .then(function(response){
              self.bratConfig=response
              self.refreshData()
            }) 
          }else{
            self.refreshData()
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        rowClick(row){
          var self=this;
          self.currentBratData.newTerms=[];
          self.currentBratData.memos=[];
          if(row.newTerms!=null){
            for(var i=0;i<row.newTerms.length;i++){
              var forArr=row.newTerms[i];
              self.currentBratData.newTerms.push(forArr);
            }
          }
          if(row.memo!=null)
          {
            self.currentBratData.memos=row.memo;
          } 
          self.currentBratData.id=row.id;
        },
        //单击注解
        submitUpdateType(){
          var self=this
          console.log(self.selectedTerm)
          updateFinalAnnotation(self.currentBratId,self.selectedTerm.tag,self.selectedTerm.type,
            self.selectedTerm.newType)
          .then((response)=>{
            if(response.succ==true){
              for(var i=0;i<self.bratDatas.length ;i++){
                if(self.bratDatas[i].id==self.currentBratId){
                  self.bratDatas.splice(i,1,response.data.bratData)
                  if(self.$refs.termDeleteDialog.addChecked){
                    if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                      var forArr=response.data.newTerms;
                      self.bratDatas[i].newTerms=[];
                      for(var j=0;j<forArr.length;j++){
                          Object.keys(forArr[j]).forEach(function(key){
                            self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                        });
                      }  
                      self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                    }else{
                      self.bratDatas[i].newTerms=[];
                      self.currentBratData.newTerms=[];
                    }
                  }
                }
              }
            }
            self.refreshData()
          })
        },
        //双击注解,然后弹出modal,modal上的按钮事件
        submitDeleteTerm(){
          var self=this;
          //当前的标注state,为INIT，PROCESSING，UN_RECOGNIZE，走apiServer
          //如果为FINISH，走数据库
          if(self.selectedTerm.currentBratState=='FINISH'){
            deleteFinalAnnotation(self.currentBratId,self.selectedTerm.tag,self.$refs.termDeleteDialog.addChecked?'NEW_TERM':'ANNOTATION')
              .then((response)=>{
                if(response.succ==true){
                  for(var i=0;i<self.bratDatas.length ;i++){
                    if(self.bratDatas[i].id==self.currentBratId){
                      self.bratDatas.splice(i,1,response.data.bratData)
                      if(self.$refs.termDeleteDialog.addChecked){
                        if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                          var forArr=response.data.newTerms;
                          self.bratDatas[i].newTerms=[];
                          for(var j=0;j<forArr.length;j++){
                              Object.keys(forArr[j]).forEach(function(key){
                                self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                            });
                          }  
                          self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                        }else{
                          self.bratDatas[i].newTerms=[];
                          self.currentBratData.newTerms=[];
                        }
                      }
                    }
                  }
                }
              self.refreshData()
            });
          }else{
          deleteAnno(self.currentBratId,self.selectedTerm.tag,self.$refs.termDeleteDialog.addChecked?'NEW_TERM':'ANNOTATION')
            .then(function(response){
              if(response.succ==true){
                for(var i=0;i<self.bratDatas.length ;i++){
                  if(self.bratDatas[i].id==self.currentBratId){
                    self.bratDatas.splice(i,1,response.data.bratData)
                    if(self.$refs.termDeleteDialog.addChecked){
                      if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                        var forArr=response.data.newTerms;
                        self.bratDatas[i].newTerms=[];
                        for(var j=0;j<forArr.length;j++){
                            Object.keys(forArr[j]).forEach(function(key){
                              self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                          });
                        }  
                        self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                      }else{
                        self.bratDatas[i].newTerms=[];
                        self.currentBratData.newTerms=[];
                      }
                    }
                  }
                }
              }
              self.refreshData();
            })
          }
        },
        //在父组件中定义该方法通过v-bind进行key/value的绑定，供子组件回调该方法
        submitTerm () {
          var self=this;
          //当前的标注state,为INIT，PROCESSING，UN_RECOGNIZE，走apiServer
          //如果为FINISH，走数据库
          if(self.selectedTerm.currentBratState=='FINISH'){
          addFinalAnno(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId,self.selectedTerm.isNewTerm?'NEW_TERM':'ANNOTATION')
            .then(function(response){
              for(var i=0;i<self.bratDatas.length ;i++){
                if(self.bratDatas[i].id==self.currentBratId){
                  self.bratDatas[i].bratData=response.data.bratData;
                  if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                    var forArr=response.data.newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                      Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }
                    self.currentBratData.newTerms=self.bratDatas[i].newTerms;
                  }
                }
              }
              self.refreshData()
            })
          }else{
            addAnno(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId,self.selectedTerm.isNewTerm?'NEW_TERM':'ANNOTATION')
            .then(function(response){
              for(var i=0;i<self.bratDatas.length ;i++){
                if(self.bratDatas[i].id==self.currentBratId){
                  self.bratDatas[i].bratData=response.data.bratData;
                  if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                    var forArr=response.data.newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                      Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }
                    self.currentBratData.newTerms=self.bratDatas[i].newTerms;
                  }
                }
              }
              self.refreshData()
            })
          }
        },
        //在父组件中定义的方法，并注入到子组件中。供子组件调用，传入相应的参数，并带参数打开modal组件
        selectText (data, callback) {
          var self=this;
          console.log(data)
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.currentBratState=data.currentBratState;
          self.getTypesFromConfig();
          self.$refs.termDialog.open(self.selectedTerm);
          self.submitTermCallBack=callback;
        },
        updateLabel(data){
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.currentBratState=data.currentBratState;
          self.selectedTerm.tag=data.tag;
          self.getTypesFromConfig();
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(self.selectedTerm.currentBratState=='FINISH'||!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.typeUpdateDialog.open(self.selectedTerm);
          }
        },
        selectLabel (data) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.currentBratState=data.currentBratState;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(self.selectedTerm.currentBratState=='FINISH'||!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        },

        recursiveType(id,dataArr){
          var self=this
          self.depth++
          var prefix=self.prefix.substring(0,(self.depth-1)*4)
          for(var k=0;k<dataArr.length;k++){
            if(dataArr[k].parentId==id){
              if(!dataArr[k].typeCode.endsWith('-unconfirmed')){
                self.selectedTerm.types.push({
                  label: prefix+dataArr[k].typeName,
                  value: dataArr[k].typeCode
                })
              }
              if(dataArr[k].hasChildren>0){
                self.recursiveType(dataArr[k].id,dataArr)
              }
            }
          }
          self.depth--
        },
        getTypesFromConfig(){
          var self=this
          if(self.selectedTerm.types==null||self.selectedTerm.types.length==0){
            configFromDb()
            .then((response)=>{
              if(response.succ==true){
                var serverTypes=response.data
                self.recursiveType('0',serverTypes)
              }
            })
          }
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          self.loading=true
          queryAnnoByStateAndTermFuzzy(self.pageIndex,self.pageSize,self.condition.state,self.condition.termText)
          .then(function(response){
            self.bratDatas=response.data.dataList;
            console.log(self.bratDatas)
            self.totalLimit=response.data.total;
            for(var i=0;i<self.bratDatas.length;i++){
              if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                var forArr=self.bratDatas[i].newTerms;
                self.bratDatas[i].newTerms=[];
                for(var j=0;j<forArr.length;j++){
                  if(forArr[j]!=null){
                    var obj=Object.keys(forArr[j]).forEach(function(key){
                      self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }
                }
              }
            }
            // if(self.bratDatas.length>0){
              self.loading=false;
            // }
          })
        },
        //调用后台的list.do接口获取bratData标注数据，以及远程读取config.json文件的bratConfig内容
        getAllData () {
          var self=this
          self.loading=true;
          if(self.condition.state==null)
          {
            self.$message({
              message:'请选择标注类型',
              type:'warning'
            })
              self.loading=false
              return
          }
          if(self.bratConfig==null){       
            config()
            .then(function(response){
              self.bratConfig=response.data;           
              queryAnnoByStateAndTermFuzzy(self.pageIndex,self.pageSize,self.condition.state,self.condition.termText)
              .then(function(response){
                self.bratDatas=response.data.dataList;
                console.log(self.bratDatas)
                self.totalLimit=response.data.total;
                for(var i=0;i<self.bratDatas.length;i++){
                  if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                    var forArr=self.bratDatas[i].newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                      if(forArr[j]!=null){
                        var obj=Object.keys(forArr[j]).forEach(function(key){
                          self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                        });
                      }
                    }
                  }
                }
                self.loading=false;
              });
            });
          }else{        
            queryAnnoByStateAndTermFuzzy(self.pageIndex,self.pageSize,self.condition.state,self.condition.termText)
            .then(function(response){
              self.totalLimit=response.data.total;
              self.bratDatas=response.data.dataList;
              for(var i=0;i<self.bratDatas.length;i++){
                if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                  var forArr=self.bratDatas[i].newTerms;
                  console.log(forArr);
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                    if(forArr[j]!=null){
                      var obj=Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }
                  }
                } 
              }
              self.loading=false;
            });
          }
        }   
      },
      components:{
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail,
        'modal-delete-term':ModalDeleteTerm,
        'modal-update-type':ModalUpdateType
      }
    }
</script>


