<template>
  <div>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <el-row  :gutter="10" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="2">
          <el-button type="primary" @click='initTable' icon="search">查询</el-button>
        </el-col>
        <el-col :span="4" :offset="1">
          <el-input v-model="condition.term" placeholder="术语名称" style="width:100%"
          @keyup.enter.native="termEnter"></el-input>
        </el-col>
        <el-col :span="4">
           <el-input v-model="condition.ID" placeholder="术语ID" style="width:100%"
          @keyup.enter.native="IDEnter"></el-input>
        </el-col>
        <el-col :span="4" >
          <el-select v-model="condition.type" placeholder="术语类型" style="width:100%"
            filterable @change="typeChange" clearable>
            <el-option
              v-for="item in condition.types"
              :key="item.typeCode"
              :label="item.typeName"
              :value="item.typeCode">
              <span style="float: left">{{item.typeCode}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeName}}</span>
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-checkbox v-model="condition.checked" size="large" @change="checkedChange">{{checkedName}}</el-checkbox>
        </el-col>
    </el-row>
    <el-table
        v-loading="loading"
        element-loading-text="加载数据"
        :data="termDatas"
        style="width:100%"  
        empty-text="暂无数据"
        highlight-current-row
        >              
        <el-table-column label="ID" property="id"  min-width="100"  header-align="center">
        </el-table-column>
        <el-table-column label="术语" property="term" min-width="160" header-align="center" >   
        </el-table-column>
        <el-table-column label="类型" property="type" min-width="160" header-align="center" >
        </el-table-column>
        <el-table-column
          prop="conceptId"
          label="同义词"
          min-width="80"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="(scope.row.conceptId === null ||scope.row.conceptId === '') ? 'primary' : 'success'"
              close-transition>
              <div v-if="scope.row.conceptId != null && scope.row.conceptId != ''">
                <i style="cursor:pointer" @click="handleSynonyms(scope.row)">{{scope.row.standardName}}</i>
                <!-- {{scope.row.standardName}} -->
              </div>
              <div v-else>
                无同义词
              </div>
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作"  min-width="250"
          header-align="center">
          <template  slot-scope="scope">
            <el-button-group>
            <el-button type="danger" size="small" 
                       @click='handleAbandonTerm(scope.row.id)'>弃用</el-button>
            <el-button type="danger" size="small"
                       @click='handleDeleteTerm(scope.row)'>删除</el-button>
            <el-button type="success" size="small"
                       @click='handleConcept(scope.row)'>同义词</el-button>
            <el-button type="primary" size="small"
                       @click='handleAnnotation(scope.row)'>上下文</el-button>
            <el-button type="primary" size="small" 
                       @click='handleUpdate(scope.row)'>更新</el-button>
            <el-button type="info" size="small" 
                       @click='splitAtomicTerm(scope.row)'>细分术语</el-button>
            </el-button-group>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" property="gmtModified" min-width="160" header-align="center" >
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination>
      <el-dialog title="同义词操作" :visible.sync='synonymVisible' size="tiny">
        <el-form :model="currentConcept" label-position="left" label-width="110px">
          <el-form-item label="当前对应概念">
            <el-input v-model="currentConcept.standName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="是否创建新概念">
            <el-switch
              :width=100
              @change="switchChange"
              v-model="currentConcept.action"
              on-color="#13ce66"
              off-color="#ff4949"
              :on-text="switchOn"
              off-text="归组"
              on-value="add"
              off-value="update">
            </el-switch>
          </el-form-item>
          <el-form-item label="标准概念集" v-show="conceptSelect">
            <el-select v-model="currentConcept.conceptValue" 
              filterable allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in currentConcept.concepts"
                :key="item.conceptId"
                :label="item.standardName"
                :value="item.conceptId">
              </el-option>
            </el-select>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cConceptConfirm">确认</el-button>
            <el-button @click='synonymVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="修改类型" :visible.sync='updateAtomicTermVisible' size="tiny" v-loading.body="loading"
      element-loading-text="持续更新中">
        <el-form :model="currentAtomicTerm" label-position="left" label-width="110px">
          <el-form-item label="原子术语">
            <el-input v-model="currentAtomicTerm.term"></el-input>
          </el-form-item>
          <el-form-item label="当前类型">
            <el-input v-model="currentAtomicTerm.type"></el-input>
          </el-form-item>
          <el-form-item label="类型列表">
            <el-select v-model="currentAtomicTerm.value" 
              filterable 
              allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in currentAtomicTerm.types"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="float: left">{{item.label}}</span>
                <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
              </el-option>
            </el-select>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cAtomicTermConfirm">确认</el-button>
            <el-button @click='updateAtomicTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="细分原子术语" :visible.sync='splitAtomicTermVisible' size="large" v-loading.body="splitAtomicTermBodyLoading">
        <el-row>
          <el-col :span="12">
              <div id="mark-result">
                <el-table
                  v-loading="splitAtomicLoading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >              
                  <el-table-column label="当前原子术语" min-width="600" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'
                        :on-select-text='selectText'
                        :on-select-label='selectLabel'
                        >
                      </malgo-brat>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
          </el-col>
          <el-col :span="12">
            <el-table
                  :data="atomicTermArr"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >              
                  <el-table-column label="拆分原子术语" prop="text" min-width="100" header-align="center" >
                  </el-table-column>
                  <el-table-column label="对应类型" prop="annotationType" min-width="100" header-align="center" >
                  </el-table-column>
                </el-table>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24" style="text-align:center">
            <el-button type="info" @click='addAtomicAndUpdateAnnotation'>保存新原子术语</el-button>
          </el-col>
        </el-row>
      </el-dialog>
      <modal-annotation ref='annotationDialog' ></modal-annotation>
      <el-dialog title="同义词组操作" :visible.sync='synonymsGroupVisible' size="large" @close="closeSynonymsGroup">
        <el-row>
          <el-col :span="24" style="text-align:center">
              <span style="font-size:20px">当前同义词组标准名:</span><i style="font-size:20px">{{standardName}}</i>    
          </el-col>
        </el-row>
        <el-button type="primary" @click='batchSynonymsUpdate'>批量更新原子术语同义词</el-button>
          <el-table
              v-loading="aloading"
              element-loading-text="加载数据"
              :data="atomicTermArrDatas"
              style="width:100%"  
              empty-text="暂无数据"
              @selection-change="ahandleSelectionChange"
              highlight-current-row
              >     
              <el-table-column
                      type="selection"
                      width="55">
              </el-table-column>         
              <el-table-column label="ID" property="id"  min-width="100"  header-align="center">
              </el-table-column>
              <el-table-column label="术语" property="term" min-width="160" header-align="center" >   
              </el-table-column>
              <el-table-column label="类型" property="type" min-width="160" header-align="center" >
              </el-table-column>
              <el-table-column
                label="同义词" property="standardName" min-width="160" header-align="center">
              </el-table-column>
          </el-table>
          <el-pagination @size-change="aHandleSizeChange" @current-change="aHandleCurrentChange"
                    :current-page.sync="aPageIndex" :page-sizes="[10,20,30,50]" :page-size="aPageSize" layout="total, sizes, prev, pager, next, jumper" :total="aTotalLimit">
          </el-pagination>
      </el-dialog>
      <el-dialog title="同义词组操作" :visible.sync='synonymBatchGroupVisible' size="tiny">
          <el-row style="text-align:center">
            <el-col :span="24">
               <el-switch
                  :width=90
                  @change="synonymsSwitchChange"
                  v-model="concept.action"
                  on-color="#13ce66"
                  off-color="#ff4949"
                  on-text="批量更新"
                  off-text="批量置空"
                  on-value="update"
                  off-value="cover">
                </el-switch>
            </el-col>
          </el-row>
          <el-row style="text-align:center">
            <el-col :span="24">
              <el-select v-model="concept.conceptValue" 
                filterable allow-create
                placeholder="请选择" style="width:80%" v-show="showConcepts">
                <el-option
                  v-for="item in concept.concepts"
                  :key="item.conceptId"
                  :label="item.standardName"
                  :value="item.conceptId">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row style="text-align:center">
            <el-col :span="24">
              <el-button type="primary" @click="synonymsBatchGroupConfirm">确认</el-button>
              <el-button @click='synonymBatchGroupVisible=false'>关闭</el-button>
            </el-col>
          </el-row>
      </el-dialog>
  </div>
</template>
<script>
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'
    import ModalAnnotation from '@/components/ModalAnnotation.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import {fuzzyAtomicTerms,getAtomicTerms,getAllConcepts,addConcept,updateConcept,deleteAtomicTerm,abandonAtomicTerm,updateAtomicTerm,clearConceptIdOfAtomicTerm} from '@/api/atomic'
    import {getStandName} from '@/api/concept'
    import {getTypes,getTermAnnotationsByType} from '@/api/type'
    import {config,configFromDb,addFinalAnno,subdivideAtomicTerm} from '@/api/annotation'
    import {getAtomicTermsByConceptId,batchUpdateConceptIdOfAtomicTerm} from '@/api/conceptGroup'
    export default{
      data () {
        return {
          switchOn:'创建',
          createOrRemove:true,
          loading:false,
          updateAtomicTermVisible:false,
          checkedName:'仅显示有同义词的数据',
          newTermDatas:null,
          termDatas:null,
          loading:false,
          pageIndex:1,
          totalPages:0,
          totalLimit:0,
          pageSize:10,        
          currentConcept:{
            concepts:[],
            conceptValue:'',
            conceptName:null,
            action:'add',
            termId:null,
            ptermId:null,
            termCode:null,
            termType:null,
            termName:null,
            category:null,
            originName:null,
            standName:null,
            conceptId:null,
            id:null
          },
          conceptSelect:false,
          condition:{
            ID:null,
            term:null,
            type:null,
            checked:true,
            types:[]
          },
          synonymVisible:false,
          bratConfig:null,
          param:{
            config:null,
            term:null,
            type:null
          },
          currentAtomicTerm:{
            type:null,
            types:[],
            term:null,
            value:null
          },
          splitAtomicTermVisible:false,
          bratDatas:[],
          brat:{
            bratData:{
              text:null
            },
            // bratConfig:null,
            id:null
          },
          bratConfig:null,
          splitAtomicLoading:false,
          selectedTerm: {
            term: null,
            types: [],
            isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null
          },
          atomicTermArr:[],
          currentBratId:null,
          newAtomicTerm:{
            text:null,
            annotationType:null,
            startPosition:null,
            endPosition:null,
            anId:null,
            option:null
          },
          splitAtomicTermBodyLoading:false,
          conceptId:null,
          aloading:false,
          aPageIndex:1,
          aTotalLimit:0,
          aPageSize:10,
          atomicTermArrDatas:[],
          synonymBatchGroupVisible:false,
          AtomicTermSynonymsMultipleSelection:[],
          concept:{
            conceptValue:null,
            concepts:[],
            action:null
          },
          showConcepts:false,
          standardName:null,
          synonymsGroupVisible:false
        }
      },
      created(){
        var self=this
        self.initTable()
        self.queryType()
      },
      methods: {
        ahandleSelectionChange(val){
          var self=this
          self.AtomicTermSynonymsMultipleSelection = val
        },
        closeSynonymsGroup(){
          var self=this
          self.initTable()
        },
        synonymsSwitchChange(){
          var self=this
          if(self.concept.action=='update'){
            self.showConcepts=true
          }else{
            self.showConcepts=false
          }
        },
        synonymsBatchGroupConfirm(){
          var self=this
          if(self.AtomicTermSynonymsMultipleSelection.length>0){
            var idArr=[] 
            Object.keys(self.AtomicTermSynonymsMultipleSelection).forEach((key)=>{
              idArr.push(self.AtomicTermSynonymsMultipleSelection[key].id)
            })
            if(self.concept.action=='cover'){
              self.concept.conceptValue=''
            }
            batchUpdateConceptIdOfAtomicTerm(idArr,self.concept.conceptValue)
            .then((response)=>{
              if(response.succ==true){
                self.initAtomicTermSynonyms()  
                self.synonymBatchGroupVisible=false
              }
            })
          }
        },
        aHandleSizeChange(val){
          var self=this;
          self.aPageSize=val;
          self.initAtomicTermSynonyms();
        },
        aHandleCurrentChange(val){
          var self=this;
          self.aPageIndex=val;
          self.initAtomicTermSynonyms();
        },
        initAtomicTermSynonyms(){
          var self=this
          self.aloading=true
          getAtomicTermsByConceptId(self.aPageIndex,self.aPageSize,self.conceptId)
           .then((response)=>{
            if(response.succ==true){
              self.atomicTermArrDatas=response.data.dataList
              self.aTotalLimit=response.data.total
              }
              self.aloading=false
           })
        },
        batchSynonymsUpdate(){
          var self=this
          if(self.AtomicTermSynonymsMultipleSelection.length>0)
          {
            self.synonymBatchGroupVisible=true
            if(self.concept.concepts.length==0){
              getAllConcepts()
              .then((response)=>{
                if(response.succ==true){
                  self.concept.concepts=response.data
                }else{
                  self.$message.error(response.msg)
                }
              })
            }
          }else{
          self.$message({
              type:'warning',
              message:'请先勾选术语记录！'
            });
          }
        },
        handleSynonyms(currentRow){
          var self=this
          self.standardName=currentRow.standardName
          self.conceptId=currentRow.conceptId
          self.synonymsGroupVisible=true
          self.initAtomicTermSynonyms()
        },
        addAtomicAndUpdateAnnotation(){
          var self=this
          self.splitAtomicTermBodyLoading=true
          subdivideAtomicTerm(self.atomicTermArr)
          .then((response)=>{
            if(response.succ==true){ 
              self.$message({
                  type:'success',
                  message:'操作成功！'
              });
              self.initTable()
            }
            self.splitAtomicTermBodyLoading=false
          });
        },
        submitTerm () {
          var self=this;
          // self.splitAtomicLoading=true
          var newAtomicTerm={}
          newAtomicTerm.text=self.selectedTerm.term
          newAtomicTerm.annotationType=self.selectedTerm.type
          newAtomicTerm.startPosition=self.selectedTerm.startOffset
          newAtomicTerm.endPosition=self.selectedTerm.endOffset
          newAtomicTerm.anId=self.currentBratId
          newAtomicTerm.option='NEW_TERM'
          // self.atomicTermArr.push({'text':self.selectedTerm.term,'annotationType':self.selectedTerm.type,
          //   'startPosition':self.selectedTerm.startOffset,'endPosition':self.selectedTerm.endOffset,'anId':self.currentBratId,'option':'NEW_TERM'})
          self.atomicTermArr.push(newAtomicTerm)
        },
        selectText (data, callback) {
          var self=this;
          console.log(data)
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.getTypesFromDb();
          self.$refs.termDialog.open(self.selectedTerm);
          self.submitTermCallBack=callback;
        },
        getTypesFromDb(){
          var self=this
          if(self.selectedTerm.types==null||self.selectedTerm.types.length==0){
            configFromDb()
            .then((response)=>{
              var serverTypes=response.data
              var i=0,leni=serverTypes.length
              for(;i<leni;i++){
                if(!serverTypes[i].typeCode.endsWith('-unconfirmed')){
                  self.selectedTerm.types.push({
                    label: serverTypes[i].typeName,
                    value: serverTypes[i].typeCode
                  })
                }
              }
            })
          }
        },
        splitAtomicTerm(currentRow){
          var self=this  
          self.splitAtomicLoading=true
          self.bratDatas=[]
          if(self.bratConfig==null){
            config()
            .then((response)=>{
              if(response.succ==true){
                self.bratConfig=response.data
                self.brat.id=currentRow.id
                self.brat.bratData.text=currentRow.term
                self.bratDatas.push(self.brat)
                self.splitAtomicLoading=false
              }
            })            
          }else{
            self.brat.id=currentRow.id
            self.brat.bratData.text=currentRow.term
            self.bratDatas.push(self.brat)
            self.splitAtomicLoading=false
          }
          self.splitAtomicTermVisible=true
        },
        IDEnter(){
          var self=this
          if(self.condition.ID!=null){
            self.condition.term=null
          }
          self.initTable()
        },
        typeChange(){
          var self=this
          if(self.condition.type!=null){
            self.condition.term=null
          }
          self.initTable()
        },
        termEnter(){
          var self=this
          self.condition.type=null
          self.condition.ID=null
          self.initTable()
        },
        cAtomicTermConfirm(){
          var self=this
          self.loading=true
          updateAtomicTerm(self.currentAtomicTerm.atomicTermId,self.currentAtomicTerm.value)
          .then((response)=>{
            if(response.succ==true){
              self.updateAtomicTermVisible=false
              self.loading=false
              self.$message({
                  type:'success',
                  message:'更新成功！'
              });
              self.initTable()
            }
          })
        },
        getTypesFromConfig(){
          var self=this
          if(self.currentAtomicTerm.types==null||self.currentAtomicTerm.types.length==0){
            configFromDb()
            .then((response)=>{
                var serverTypes=response.data
                var i=0,leni=serverTypes.length
                for(;i<leni;i++){
                  if(!serverTypes[i].typeCode.endsWith('-unconfirmed')){
                    self.currentAtomicTerm.types.push({
                      label: serverTypes[i].typeName,
                      value: serverTypes[i].typeCode
                    })
                  }
                }
            })
          }
        },
        handleUpdate(currentRow){
          var self=this
          self.getTypesFromConfig()
          self.currentAtomicTerm.atomicTermId=currentRow.id
          self.currentAtomicTerm.term=currentRow.term
          self.currentAtomicTerm.type=currentRow.type
          // self.currentAtomicTerm.value=currentRow.type
          self.updateAtomicTermVisible=true
        },
        checkedChange(){
          var self=this
          if(self.condition.checked==false){
            self.checkedName='仅显示无同义词的数据'
          }else{
            self.checkedName='仅显示有同义词的数据'
          }
          self.initTable()
        },
        initConfig(){
          var self=this
          
        },
        getBratConfig(){
          return config()
        },
        handleAnnotation(currentRow){
          var self = this;
          self.param.type=currentRow.type
          self.param.term=currentRow.term
          self.$refs.annotationDialog.open(self.param,self.getBratConfig
            // , () => {
            // return getTermAnnotationsByType(currentRow.type,currentRow.term)
          // }
          );
        },
        queryType(){
          var self=this;
          getTypes()
          .then((response)=>{
            var serverTypes=response.data
            var i=0,leni=serverTypes.length
              for(;i<leni;i++){
                if(!serverTypes[i].typeCode.endsWith('-unconfirmed')){
                  self.condition.types.push({
                    typeName: serverTypes[i].typeName,
                    typeCode: serverTypes[i].typeCode
                  })
                }
              }
          })
        },
        cConceptConfirm(){
          var self=this
          if(self.currentConcept.action=='add'){
            if(self.currentConcept.conceptId==null||self.currentConcept.conceptId==''){
              addConcept(self.currentConcept.id,self.currentConcept.originName)
              .then((response)=>{
                if(response.succ==true){
                  self.$message({
                      type:'success',
                      message:'新增成功！'
                  });
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
              })
            }else{
              if(self.createOrRemove){
                //当前创建操作
                self.$message({
                    type:'warning',
                    message:'已有对应概念，无法再次新增！'
                });
              }else{
                //当前是移除，即置空操作
                clearConceptIdOfAtomicTerm(self.currentConcept.id)
                .then((response)=>{
                  if(response.succ==true){
                      self.$message({
                        type:'success',
                        message:'移除成功！'
                    });
                  }
                  self.initTable()
                })
              }
            }
            self.synonymVisible=false
          }
          if(self.currentConcept.action=='update'){
            if(self.currentConcept.conceptValue==null||self.currentConcept.conceptValue==''){
              self.$message({
                  type:'warning',
                  message:'请先选择下拉框的选项或者输入概念！'
              });
              return
            }
            var conceptArr=self.currentConcept.concepts;
            if(conceptArr.length>0){
              //循环当前下拉框中的所有同义词记录
              for(var k=0;k<conceptArr.length;k++){
                //如果当前选中对象的code等于下拉框选中的对象的code，则获取当前对象的标准名称
                if(conceptArr[k]['conceptId']===self.currentConcept.conceptValue){
                    console.log(conceptArr[k]['standardName'])//为当前的下拉框的显示值
                    self.currentConcept.conceptName=conceptArr[k]['standardName']
                    break
                }
                //如果前端并非下拉框集合中选中，而是手动输入某个新的概念
                if(conceptArr[k]['standardName']!=self.currentConcept.conceptValue&&k===(conceptArr.length-1)){
                  var cup=self.currentConcept.conceptValue;
                  self.currentConcept.conceptValue=self.currentConcept.conceptName
                  self.currentConcept.conceptName=cup
                }
              }
            }
            updateConcept(self.currentConcept.id,self.currentConcept.conceptValue,self.currentConcept.conceptName)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                    type:'success',
                    message:'更新成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
              self.synonymVisible=false
            })
          }
        },
        switchChange(){
          var self=this
          if(self.currentConcept.action=='add')
          {
            self.conceptSelect=false
          }
          if(self.currentConcept.action=='update')
          {
            self.conceptSelect=true
            getAllConcepts()
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.concepts=response.data
              }else{
                self.$message.error(response.msg)
              }
            })
          }
        },
        handleConcept(currentRow){
          //同义词操作
          var self=this
          self.currentConcept.standName=null
          self.synonymVisible=true
          self.currentConcept.originName=currentRow.term;
          self.currentConcept.conceptId=currentRow.conceptId;
          self.currentConcept.id=currentRow.id;
          //如果当前行的conceptId已绑定同义词，则弹出框显示'移除同义词'/'归组'
          if(currentRow.conceptId!=null&&currentRow.conceptId!=''){
            getStandName(currentRow.conceptId)
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.standName=response.data.standardName
              }
            })
            self.switchOn='移除同义词'
            self.createOrRemove=false
          }else{
            //弹出框显示 '新建'/'归组'
            self.switchOn='创建'
            self.createOrRemove=true
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.initTable();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.initTable();
        },
        initTable(){
          var self=this
          self.loading=true
          if(self.condition.term!=null&&self.condition.type==null)
          {
            fuzzyAtomicTerms(self.pageIndex,self.pageSize,self.condition.term,self.condition.type,self.condition.checked)
            .then((response)=>{
              if(response.succ==true){
                self.totalLimit=response.data.total;
                self.termDatas=response.data.dataList
              }
              self.loading=false
            })
          }else{
            getAtomicTerms(self.pageIndex,self.pageSize,self.condition.term,self.condition.type,self.condition.ID,self.condition.checked)
            .then((response)=>{
              if(response.succ==true){
                self.totalPages=response.data.pages;
                self.totalLimit=response.data.total;
                self.termDatas=response.data.dataList  
              }
              self.loading=false
            })
          }
        },
        handleDeleteTerm(currentRow){
            var self=this
            console.log(currentRow)
            self.$confirm('确认删除该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
              deleteAtomicTerm(currentRow.id,currentRow.term,currentRow.type)
              .then((response)=>{
                if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'删除成功！'
                  });
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
              })
            })
        },
        handleAbandonTerm(pId){
            var self=this;
            self.$confirm('确认遗弃该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             abandonAtomicTerm(pId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
      ,
      components:{
        'modal-annotation':ModalAnnotation,
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail
      }
    }
</script>



